import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { Provider } from 'src/app/core/models/provider.model';
import { ProvidersService } from 'src/app/core/services/workFlowModule/providers.service';
import { ProviderTypeService } from 'src/app/core/services/workFlowModule/provider-type.service';
import { ProviderType } from 'src/app/core/models/provider-type.model';

@Component({
  selector:  'app-modal-provider',
  templateUrl:  './modal-provider.component.html',
  styleUrls:  ['./modal-provider.component.css']
})
export class ModalProviderComponent implements OnInit {
  @Input()
  public idOrder: number;
  @Input()
  public idActivity: string;
  @Input()
  public activityName: string;
  @Output()
  public providerForm: FormGroup;
  public loading: boolean;
  public providers: Array<Provider>;
  public providerTypes: Array<ProviderType> = [];
  public error: string;

  constructor(private providerService: ProvidersService<Provider>) {
    this.loading = false;
  }

  ngOnInit() {
    this.getProvidersOrder();
  }

  private getProvidersOrder() {
    this.loading = true;
    this.providerService.getProvidersByIdOrder(this.idOrder + '').pipe (
      finalize (
        () => this.loading = false
      )
    ).subscribe(
    (providers: Array<Provider>) => {
        if (providers != null) {
          this.providers = providers;
        }
      },
      error => {
      }
    );
  }

  onReloadEvent(reload: boolean) {
    console.log(reload);
    this.getProvidersOrder();
  }

}
