import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SubactivityCreditOrder } from 'src/app/core/models/subactivity-credit-order.model';
import { SubactivityCreditOrderService } from 'src/app/core/services/workFlowModule/subactivity-credit-order.service';
import { finalize } from 'rxjs/operators';
import { FormBuilder, FormArray, FormGroup, FormControl } from '@angular/forms';
import { ActivityDocument } from 'src/app/core/models/activity-document.model';
import { CreditDocument } from 'src/app/core/models/credit-document';
import { CreditDocumentService } from 'src/app/core/services/workFlowModule/credit-document.service';
import { ActivityDocumentService } from 'src/app/core/services/workFlowModule/activity-document.service';
import { CreditOrderActivity } from 'src/app/core/models/credit-order-activity.model';
import { GlobalConstants } from 'src/app/shared/constants';

@Component({
  selector: 'app-modal-subactivity',
  templateUrl: './modal-subactivity.component.html',
  styleUrls: ['./modal-subactivity.component.css']
})
export class ModalSubactivityComponent implements OnInit {

  @Input()
  public idActivity: string;
  @Input()
  public idCreditActivity: number;
  @Input()
  public idOrder: number;
  @Input()
  public activityName: string;
  @Output()
  public reload = new EventEmitter<boolean>();
  public activityDocument: Array<ActivityDocument>;
  public subactivities: Array<SubactivityCreditOrder>;
  public loading: boolean;
  public subactivitiesForm: FormGroup;
  public error: string;
  public showDateField: boolean;

  constructor(private subactivityService: SubactivityCreditOrderService<SubactivityCreditOrder>,
              private creditDocumentService: CreditDocumentService<CreditDocument>,
              private activityDocumentService: ActivityDocumentService<ActivityDocument>,
              private formBuilder: FormBuilder) {
    this.subactivitiesForm = this.formBuilder.group({
        checkeds : new FormArray([]),
        uncheckeds : new FormArray([])
    });

    this.loading = false;
  }

  ngOnInit() {
    this.getSubactivities();
    this.getActivityDocument();
  }

  private getSubactivities() {
    this.loading = true;
    this.subactivityService.getSubactivityByActivityId(this.idActivity + '', this.idOrder + '').pipe (
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (credits: Array<SubactivityCreditOrder>) => {
        this.subactivities = credits;
      },
      error => {
        console.log(error.error);
      }
    );
  }

  public onCheckboxChange(e: any) {
    const checkArray: FormArray = this.subactivitiesForm.get('checkeds') as FormArray;
    const uncheckArray: FormArray = this.subactivitiesForm.get('uncheckeds') as FormArray;

    if (e.target.checked) {
      checkArray.push(new FormControl(e.target.value));
    } else {
      let i = 0;
      checkArray.controls.forEach((item: FormControl) => {
        if (item.value === e.target.value) {
          checkArray.removeAt(i);
          return;
        }
        i++;
      });
      uncheckArray.push(new FormControl(e.target.value));
    }
  }

  private onSubmit() {
    const checkArray: FormArray = this.subactivitiesForm.get('checkeds') as FormArray;
    let checkeds = '';
    checkArray.controls.forEach((item: FormControl) => {
      checkeds += item.value + ',';
    });
    const uncheckArray: FormArray = this.subactivitiesForm.get('uncheckeds') as FormArray;
    let uncheckeds = '';
    uncheckArray.controls.forEach((item: FormControl) => {
      uncheckeds += item.value + ',';
    });
    const formData = new FormData();
    formData.append('idOrder', this.idOrder + '');
    formData.append('idActivity', this.idActivity + '');
    formData.append('subactivitiesChecked', checkeds);
    formData.append('subactivitiesUnchecked', uncheckeds);
    this.subactivityService.update(formData, 'save').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response === 1) {
          this.submitDocumentValue();
          this.getSubactivities();
          this.reload.next(true);
          checkeds = '';
          uncheckeds = '';

        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public submitDocumentValue() {
    if (this.showDateField && this.activityDocument != null) {
      for (const activityDoc of this.activityDocument) {
        let creditDocument = activityDoc.creditDocument;
        if (creditDocument != null) {
          creditDocument.DocumentDate = this.subactivitiesForm.get('documentDate' + activityDoc.idDocument).value;
          creditDocument.IdRoute = this.subactivitiesForm.get('idRoute' + activityDoc.idDocument).value;
          creditDocument.UpdatedUser = GlobalConstants.obtenerNombreUsuario();
          this.updateDocumentDate(creditDocument);
        } else {
          creditDocument = new CreditDocument();
          creditDocument.CreditOrderId = this.idOrder;
          creditDocument.CreditOrderActivityId = this.idCreditActivity;
          creditDocument.DocumentId = activityDoc.idDocument;
          creditDocument.DocumentDate =  this.subactivitiesForm.get('documentDate' + activityDoc.idDocument).value;
          creditDocument.IdRoute =  this.subactivitiesForm.get('idRoute' + activityDoc.idDocument).value;
          creditDocument.CreationUser = GlobalConstants.obtenerNombreUsuario();
          this.saveDocumentDate(creditDocument);
        }
      }
    }
  }

  public getActivityDocument() {
    this.loading = true;
    const idActivity = this.idActivity + '';
    this.activityDocumentService.getListActivitiesDocument(idActivity).pipe(
      finalize (
        () => this.loading = false
      )).subscribe(
        (response: Array<ActivityDocument>) => {
          if (response != null) {
            this.activityDocument = response;
            this.showDateField = true;
            this.handleActiveDocumentDate();
            this.setDocumentCreditActivity();
          }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private setDocumentCreditActivity() {
    for (const activityDoc of this.activityDocument) {
      this.getCreditDocument(activityDoc);
    }
  }

  private getCreditDocument(activityDocument: ActivityDocument) {
    const idDocument = activityDocument.idDocument + '';
    const idCreditActivity = this.idCreditActivity + '';
    this.loading = true;
    this.creditDocumentService.getCreditDocument(idCreditActivity, this.idOrder + '', idDocument).pipe(
      finalize (
        () => this.loading = false
      )).subscribe(
        (response: CreditDocument) => {
          if (response != null) {
            activityDocument.creditDocument = response;
          }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private handleActiveDocumentDate() {
    if (this.activityDocument != null) {
      for (const doc of this.activityDocument) {
        const fc = new FormControl('');
        this.subactivitiesForm.addControl('documentDate' + doc.idDocument, fc);
        this.subactivitiesForm.addControl('idRoute' + doc.idDocument, fc);
      }
    }
  }

  private handleRemoveDocumentDate() {
    if (this.activityDocument != null) {
      for (const doc of this.activityDocument) {
        this.subactivitiesForm.removeControl('documentDate' + doc.idDocument);
        this.subactivitiesForm.removeControl('idRoute' + doc.idDocument);
      }
    }
  }

  public updateDocumentDate(document: any) {
    this.loading = true;
    this.creditDocumentService.update(document, 'putSaveDate').pipe(
      finalize (
        () => this.loading = false
      )).subscribe((response: any) => {
        if (response != null) {
          document = response;
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public saveDocumentDate(document: any) {
    this.loading = true;
    this.creditDocumentService.post(document, 'postSaveDate').pipe(
      finalize (
        () => this.loading = false
      )).subscribe((response: any) => {
        if (response != null) {
          document = response;
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }


}
