import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ClientEnsureService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
      super();
      this.apiUrl = environment.apiWorkflowUrl;
      this.httpClient = http;
      this.resource = 'ClientOrder/';
  }

  public postClientInsured(client: T): Observable<any> {
    const path = 'postClientInsured';
    return this.executePost(path, client, null).pipe(
      map((res: any) => res)
    );
  }

}
