import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './security/login/login.component';
import { DashboardComponent } from './shared/dashboard/dashboard.component';
import { ChangePasswordComponent } from './security/ChangePassword/ChangePassword.component';
import { ProfileChangePasswordComponent } from './security/profile-change-password/profile-change-password.component';
import { NotFoundComponent } from './shared/components/not-found/not-found.component';
// import { ProductionModule } from './production/production.module';
// Se crea variable para cargar modulo por carga peresoza (LazyLoad)
const productionModule = () => import('./production/production.module').then(mod => mod.ProductionModule);

const appRoutes: Routes = [

    {
        path: 'login',
        component: LoginComponent
    },
    {
        path: 'ChangePassword',
        component: ChangePasswordComponent
    },
    {
        path: 'dashboard',
        component: DashboardComponent,
        data: { title: 'Inicio' },
        children: [
            {
                path: 'reports',
                loadChildren: './reports/reports.module#ReportsModule'
            },
            {
                path: 'Workflow',
                loadChildren: './work-flow/work-flow.module#WorkFlowModule'
            },
            {
                path: 'production',
                loadChildren: productionModule
            },
            {
                path: 'profileChangePassword',
                component: ProfileChangePasswordComponent
            },
            {
                path: 'notFound',
                component: NotFoundComponent
            },
            {
                path: '',
                redirectTo: '/dashboard/notFound',
                pathMatch: 'full'
            },
            {
                path: '**',
                redirectTo: '/dashboard/notFound',
                pathMatch: 'full'
            }
        ]
    },
    {
        path: '',
        redirectTo: '/login',
        pathMatch: 'full'
    },
    {
        path: '**',
        redirectTo: '/login',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [
        RouterModule.forRoot(appRoutes, {useHash : true})
    ],
    exports: [
        RouterModule
    ]
})
export class AppRoutingModule {

}
