import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { NotesCriticalCases } from '../../models/seller-segments.model';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class NotesCriticalCasesService extends ServiceService<any>{

  constructor(private http: HttpClient) { 
    super();
    this.apiUrl = environment.apiReportsUrl;
    this.httpClient = http;
    this.resource = 'NotesCriticalCases';
  }

  public getNotesCriticalCasesByCase(idCase: number) : Observable<NotesCriticalCases>{
    const path = `/GetNotesCriticalCasesByCase`;
    let params = new HttpParams();
    params = params.append('idCase', String(idCase));
    return this.getParams(path, params).pipe(map((resp: NotesCriticalCases) => resp));
  }

  public postNotesCriticalCases(note: NotesCriticalCases): Observable<NotesCriticalCases>{
    const path = ``;
    return this.post(note ,path).pipe(map((resp: NotesCriticalCases) => resp));
  }

  public putNotesCriticalCases(note: NotesCriticalCases): Observable<Boolean>{
    return this.httpClient.put(this.getFullPath(), note).pipe(
      map((res: Boolean) => res)
    );
  }

  public deleteNotesCriticalCases(idCase: number): Observable<Boolean>{
    let params = new HttpParams();
    params = params.append('IdNote', String(idCase));
    return this.httpClient.delete(this.getFullPath(), {  params: params }).pipe(
      map((res: any) => res)
    );
  }
}
