import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ClientOrder } from 'src/app/core/models/client-order.model';
import { ClientOrderService } from 'src/app/core/services/workFlowModule/client-order.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { Property } from 'src/app/core/models/property.model';
import { PropertyService } from 'src/app/core/services/workFlowModule/property.service';
import { PropertyAsset } from 'src/app/core/models/property-assets.model';


declare var $: any;

@Component({
  selector: 'app-modal-client-update',
  templateUrl: './modal-client-update.component.html',
  styleUrls: ['./modal-client-update.component.css']
})
export class ModalClientUpdateComponent implements OnInit {

  @Input()
  public idOrder: number;
  @Input()
  public idActivity: string;
  @Input()
  public activityName: string;
  @Output()
  public reload = new EventEmitter<boolean>();
  public clientForm: FormGroup;
  
  public loading: boolean;
  public clientOrder: ClientOrder;
  public property: Property;
  public error: string;
  public classFocused = '';
  public buyingProject: boolean;
  public cantPhones = 0;
  public cantEmail = 0;
  public cantMovil = 0;
  public RealEstateAssets: PropertyAsset[] = [];

  constructor(private clientOrderService: ClientOrderService<ClientOrder>,
              
              private propertyService: PropertyService<Property>,
              private formBuilder: FormBuilder) {
    this.clientForm =  new FormGroup({
      clientName: new FormControl('', Validators.required),
      clientDocument: new FormControl('', Validators.required),
      clientTypeDocument : new FormControl('', Validators.required),
      clientCity: new FormControl('', Validators.required),
      clientAddress: new FormControl('', Validators.required),
      clientGender: new FormControl('', Validators.required),
      initContactTime: new FormControl('', Validators.required),
      endContactTime: new FormControl('', Validators.required),
      employee: new FormControl('', Validators.required),
      alternativeContactName: new FormControl('', Validators.required),
      relatedReason: new FormControl('', Validators.required),
      alternativeContactMovil: new FormControl('', Validators.required),
      alternativeContactPhone: new FormControl('', Validators.required),
      alternativeContactEmail: new FormControl('', Validators.required),
      addressSaleProperty: new FormControl('', Validators.required),
      propertyType: new FormControl('', Validators.required),
      financialValue: new FormControl('', Validators.required),
      buyingProject: new FormControl('', Validators.required),
      financed: new FormControl('', Validators.required)
    });
    this.loading = false;
  }

  ngOnInit() {
    this.getClientOrder();

  }

  private handleDynamicAddControl(position: number, nameControl: string, value: string) {
      const fc = new FormControl(value);
      this.clientForm.addControl(nameControl + position, fc);
  }

  private handleDynamicRemoveControl(position: number, nameControl: string) {
      this.clientForm.removeControl(nameControl + position);
  }

  private getProperty() {
    this.loading = true;
    this.propertyService.getPropertyByOrderId(this.idOrder + '').pipe (
      finalize(
        () => {
          this.loading = false;
          this.createContactDataControls();
        }
      )
    ).subscribe(
      (property: Property) => {
        if (property != null) {
          this.property = property;
          this.RealEstateAssets = this.property.PropertyAssetModels;
          this.fillFormProperty();
          this.fillAssetsControls();
        }
      },
      error => {
        console.log(error.error);
      }
    );
  }
  private getClientOrder() {
    this.loading = true;
    this.clientOrderService.getClientByIdOrder(this.idOrder + '').pipe (
      finalize(
        () => {
          this.loading = false;
          this.createContactDataControls();
        }
      )
    ).subscribe(
      (client: ClientOrder) => {
        this.clientOrder = client;
        this.fillClientForm();
        this.getProperty();
      },
      error => {
        console.log(error.error);
      }
    );
  }
    public createContactDataControls() {
    const phones = this.clientOrder.ClientPhone.split('|');
    const emails = this.clientOrder.ClientEmail.split('|');
    const mobiles = this.clientOrder.ClientMovil.split('|');
    if (phones != null) {
      for (let i = 0; i < phones.length; i++) {
        this.handleDynamicAddControl(i, 'clientPhone', phones[i]);
      }
      this.cantPhones = phones.length;
    }
    if (emails != null) {
      for (let i = 0; i < emails.length; i++) {
        this.handleDynamicAddControl(i, 'clientEmail', emails[i]);
      }
      this.cantEmail = emails.length;
    }
    if (mobiles != null) {
      for (let i = 0; i < mobiles.length; i++) {
        this.handleDynamicAddControl(i, 'clientMovil', mobiles[i]);
      }
      this.cantMovil = mobiles.length;
    }
  }

  private saveProperty() {
    this.propertyService.post(this.property, 'save').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.reload.next(true);
          const idModal: string = '#' + this.idActivity + 'modal';
          $(idModal).modal('hide');
        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private updateProperty() {
    this.propertyService.update(this.property, 'update').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.reload.next(true);
        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private updateClient() {
    this.clientOrderService.update(this.clientOrder, 'update').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.reload.next(true);
        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }
  refresh(): void {
    window.location.reload();
}
  private onSubmit(data: any) {
    if (this.validateForm(data)) {
      this.mapNewModel(data);
      this.updateClient();
      if (this.property.PropertyId > 0) {
        this.updateProperty();
      } else {
        this.saveProperty();
      }
      this.refresh();
    }
  }

  private validateForm(data: any) {
    if (data.clientName !== '' && data.clientDocument !== '' && data.clientTypeDocument !== '') {
          return true;
    }
  }

  private mapNewModel(data: any) {
     if (this.clientOrder == null) {
       this.clientOrder = new ClientOrder();
     }
     if (this.property == null) {
       this.property = new Property();
     }
    this.clientOrder.ClientName = data.clientName;
    this.clientOrder.ClientDocument =	data.clientDocument;
    this.clientOrder.ClientTypeDocument =data.clientTypeDocument;
    this.clientOrder.ClientAddress = data.clientAddress;
    this.clientOrder.ClientGender = data.clientGender;
     this.clientOrder.ClientEmail = this.getClientEmails();
     this.clientOrder.ClientMovil = this.getClientMovils();
     this.clientOrder.ClientPhone = this.getClientPhones();
    this.clientOrder.InitContactTime =	data.initContactTime;
    this.clientOrder.EndContactTime =	data.endContactTime;
    this.clientOrder.AlternativeContactName = data.alternativeContactName;
    this.clientOrder.RelatedReason =	data.relatedReason;
    this.clientOrder.AlternativeContactMovil =	data.alternativeContactMovil;
    this.clientOrder.AlternativeContactPhone =	data.alternativeContactPhone;
    this.clientOrder.AlternativeContactEmail =	data.alternativeContactEmail;
    this.property.PropertyAddress =	data.addressSaleProperty;
    this.property.PropertyType =data.propertyType;
    this.property.FinancialValue =data.financialValue;
     this.property.BuyingProject = data.BuyingProject;
     this.property.FinanciedOrManaged = data.financed;
     this.property.CreditOrderId = this.idOrder ;
     this.property.PropertyAssetModels = this.getAssetsProperty();
  }

  private fillClientForm() {
    this.clientForm = this.formBuilder.group({
      clientName: this.clientOrder.ClientName,
      clientDocument: this.clientOrder.ClientDocument,
      clientTypeDocument : this.clientOrder.ClientTypeDocument,
      clientCity: this.clientOrder.ClientCity,
      clientAddress: this.clientOrder.ClientAddress,
      clientGender: this.clientOrder.ClientGender,
      employee: '',
      initContactTime: this.clientOrder.InitContactTime,
      endContactTime: this.clientOrder.EndContactTime,
      alternativeContactName: this.clientOrder.AlternativeContactName,
      relatedReason: this.clientOrder.RelatedReason,
      alternativeContactMovil: this.clientOrder.AlternativeContactMovil,
      alternativeContactPhone: this.clientOrder.AlternativeContactPhone,
      alternativeContactEmail: this.clientOrder.AlternativeContactEmail,
      addressSaleProperty: '',
      propertyType: '',
      financialValue: '',
      buyingProject: '',
      financed: ''
    });
  }

  public fillFormProperty() {
    if (this.property != null && this.property !== undefined) {
      this.clientForm = this.formBuilder.group({
        clientName: this.clientOrder.ClientName,
        clientDocument: this.clientOrder.ClientDocument,
        clientTypeDocument : this.clientOrder.ClientTypeDocument,
        clientCity: this.clientOrder.ClientCity,
        clientAddress: this.clientOrder.ClientAddress,
        clientGender: this.clientOrder.ClientGender,
        employee: '',
        initContactTime: this.clientOrder.InitContactTime,
        endContactTime: this.clientOrder.EndContactTime,
        alternativeContactName: this.clientOrder.AlternativeContactName,
        relatedReason: this.clientOrder.RelatedReason,
        alternativeContactMovil: this.clientOrder.AlternativeContactMovil,
        alternativeContactPhone: this.clientOrder.AlternativeContactPhone,
        alternativeContactEmail: this.clientOrder.AlternativeContactEmail,
        addressSaleProperty: this.property.PropertyAddress,
        propertyType: this.property.PropertyType,
        financialValue: this.property.FinancialValue,
        buyingProject: this.property.BuyingProject,
        financed: this.property.FinanciedOrManaged
      });
    }
  }

  private showFinanced(event: any) {
    this.buyingProject = event;
  }

  public getAssetsProperty() {
    for (let i = 0; i < this.RealEstateAssets.length; i++) {
      const idAsset = this.RealEstateAssets[i].PropertyExtraAssetsId;
      const propertyAsset = {
        PropertyExtraAssetsId: idAsset,
        Nomenclature: this.clientForm.get('nomenclature' + i).value,
        RealStateRegistration: this.clientForm.get('realStateRegistration' + i).value,
        AssetsType:  this.clientForm.get('extraType' + i).value,
        PropertyId: this.property.PropertyId
      } as PropertyAsset;
      this.RealEstateAssets[i] = propertyAsset;
    }
    return this.RealEstateAssets;
  }

  public setAssetsProperty() {
    for (let i = 0; i < this.RealEstateAssets.length; i++) {
      const idAsset = this.RealEstateAssets[i].PropertyExtraAssetsId;
      var idp=0;
      if (this.property != null) {
        idp=this.property.PropertyId;
      }
      const propertyAsset = {
        PropertyExtraAssetsId: idAsset,
        Nomenclature: this.clientForm.get('nomenclature' + i).value,
        RealStateRegistration: this.clientForm.get('realStateRegistration' + i).value,
        AssetsType:  this.clientForm.get('extraType' + i).value,
        PropertyId: idp
      } as PropertyAsset;
      this.RealEstateAssets[i] = propertyAsset;
    }
    return this.RealEstateAssets;
  }

  public addClientEmail(value: string) {
    this.handleDynamicAddControl(this.cantEmail, 'clientEmail', value);
    this.cantEmail++;
  }

  public addClientPhone(value: string) {
    this.handleDynamicAddControl(this.cantPhones, 'clientPhone', value);
    this.cantPhones++;
  }

  public addClientMovil(value: string) {
    this.handleDynamicAddControl(this.cantMovil, 'clientMovil', value);
    this.cantMovil++;
  }

  public removeClientMovil() {
    if (this.cantMovil > 0) {
      this.cantMovil--;
      this.handleDynamicRemoveControl(this.cantMovil, 'clientMovil');
    }
  }

  public removeClientPhone() {
    if (this.cantPhones > 0) {
      this.cantPhones--;
      this.handleDynamicRemoveControl(this.cantPhones, 'clientPhone');
    }
  }

  public removeClientEmail() {
    if (this.cantEmail > 0) {
      this.cantEmail--;
      this.handleDynamicRemoveControl(this.cantEmail, 'clientEmail');
    }
  }

  public getClientMovils() {
    let mobiles = '';
    for (let i = 0; i < this.cantMovil; i++) {
      mobiles += this.clientForm.get('clientMovil' + i).value + '|';
    }
    mobiles = mobiles.substring(0, mobiles.length - 1);
    return mobiles;
  }

  public getClientPhones() {
    let phones = '';
    for (let i = 0; i < this.cantPhones; i++) {
      phones += this.clientForm.get('clientPhone' + i).value + '|';
    }
    phones = phones.substring(0, phones.length - 1);
    return phones;
  }

  public getClientEmails() {
    let email = '';
    for (let i = 0; i < this.cantEmail; i++) {
      email += this.clientForm.get('clientEmail' + i).value + '|';
    }
    email = email.substring(0, email.length - 1);
    return email;
  }

  public addRealEstateAssets() {
    if(this.RealEstateAssets.length>0){
      console.log("entreeee");
      this.setAssetsProperty();
    }
    

      if (this.property != null) {
      this.RealEstateAssets.push({
        PropertyExtraAssetsId: 0,
        Nomenclature: '',
        RealStateRegistration: '',
        AssetsType: '',
        PropertyId: this.property.PropertyId
      });
    } else {
      this.RealEstateAssets.push({
        PropertyExtraAssetsId: 0,
        Nomenclature: '',
        RealStateRegistration: '',
        AssetsType: '',
        PropertyId: 0
      });
    }
    this.fillAssetsControls();
  }

  public removeRealEstateAssets(i: number) {
    this.RealEstateAssets.splice(i, 1);
    this.fillAssetsControls();
  }

  public fillAssetsControls() {
    if (this.RealEstateAssets != null && this.RealEstateAssets.length>0) {
      for (let i = 0; i < this.RealEstateAssets.length; i++) {
       
        this.handleDynamicRemoveControl(i, 'nomenclature');
        this.handleDynamicAddControl(i, 'nomenclature', this.RealEstateAssets[i].Nomenclature);
        this.handleDynamicRemoveControl(i, 'realStateRegistration');
        this.handleDynamicAddControl(i, 'realStateRegistration', this.RealEstateAssets[i].RealStateRegistration);
        this.handleDynamicRemoveControl(i, 'extraType');
        this.handleDynamicAddControl(i, 'extraType', this.RealEstateAssets[i].AssetsType);
      }
    }
  }

  public counter(cant: number) {
    return new Array(cant);
  }

}
