import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GeneralDataReportsService } from 'src/app/core/services/workFlowModule/general-data-reports.service';
import { finalize } from 'rxjs/operators';
import { InformationCase, NotesCriticalCases } from 'src/app/core/models/seller-segments.model';
import { NotesCriticalCasesService } from 'src/app/core/services/reports/notes-critical-cases.service';
import { FormGroup, FormControl } from '@angular/forms';
import { GlobalConstants } from 'src/app/shared/constants';

declare const $: any;

@Component({
  selector: 'app-case-details',
  templateUrl: './case-details.component.html',
  styleUrls: ['./case-details.component.css']
})
export class CaseDetailsComponent implements OnInit {

  public commentForm: FormGroup;
  
  constructor(private _activatedRoute: ActivatedRoute,
    private _route: Router,
    private _generalReports: GeneralDataReportsService,
    private _notesService: NotesCriticalCasesService) 
    { 
      this.commentForm = new FormGroup({
        description: new FormControl (''),
        // sendTo: new FormControl (''),
        causal : new FormControl(''),
        idCase : new FormControl(''),
        idNote : new FormControl(''),
      });
    }

  public idCase: number;
  public loading: boolean;
  public informationCase: InformationCase;
  public error: string;
  public classRequiredComment = 'warning';
  public classRequiredState = 'warning';
  public classRequiredFile = 'primary';
  public btnSaveOrUpdate = 'GUARDAR CAMBIOS';

  ngOnInit() {
    this._activatedRoute.queryParams.subscribe(params => {
      this.idCase = params['idCase'];
      this.getCaseDetails();
    });
  }

  private getCaseDetails() {
    this.loading = true;
    this._generalReports.GetInfomationCase(this.idCase)
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : InformationCase) =>{
      this.informationCase = resp;
      this.getInfoNote();
    },
    error => {
      this.error = error.error;
    });
  }

  public getInfoNote() {

    if(this.informationCase.Estado) {
      this._notesService.getNotesCriticalCasesByCase(this.informationCase.Id_Seguimiento).subscribe((resp : NotesCriticalCases) => {
        this.commentForm.setValue(
          {
            idCase : this.informationCase.Id_Seguimiento, 
            description: resp.Comentarios, 
            // sendTo: resp.AtribuibleA, 
            causal: resp.Id_Causal,
            idNote : resp.Id_Nota
          });
          this.btnSaveOrUpdate = 'ACTUALIZAR CAMBIOS';
      })
    } else{
      this.commentForm.setValue({idCase : this.informationCase.Id_Seguimiento, description: '', causal: '', idNote: 0});
      this.btnSaveOrUpdate = 'GUARDAR CAMBIOS';
    }
    
    // $('#numberCase').text(this.informationCase.tracking_numero_radicado);
    // $('#ManageCasetModal').modal({
    //   show: 'true'
    // });
  }

  public onSubmit(formComment: any) {

    if (formComment.description !== '' && formComment.causal !== '') {
      let model: NotesCriticalCases = {
        Id_Caso: this.informationCase.Id_Seguimiento,
        Comentarios: formComment.description,
        Id_Causal: formComment.causal,
        AtribuibleA: null,
        Id_Usuario: GlobalConstants.obtenerIdUsuario(),
        Id_Nota: formComment.idNote
      };

      if(formComment.idNote == '' || formComment.idNote === 0) {
        this.saveComment(model);
      } else {
        this.updateComment(model)
      }
      
      const idModal: string = '#ManageCasetModal';
      $(idModal).modal('hide');
    } else {
      this.classRequiredComment = formComment.description !== '' ? 'success' : 'error';
      // this.classRequiredState = formComment.sendTo !== '' ? 'success' : 'error';
      this.classRequiredState = formComment.causal !== '' ? 'success' : 'error';
    }
  }

  public saveComment(model: NotesCriticalCases) {
    // console.log(model);
    this.loading = true;
    this._notesService.postNotesCriticalCases(model).pipe(
      finalize(() => {
        this.getInfoNote();
        this.loading = false;
      })
    ).subscribe((resp : NotesCriticalCases) => {
      // console.log(resp);
    });
  }

  public updateComment(model: NotesCriticalCases) {
    this.loading = true;
    this._notesService.putNotesCriticalCases(model).pipe(
      finalize(() => {
        this.getInfoNote();
        this.loading = false;
      }) 
    ).subscribe((resp : boolean) => {
      
    });
  }

  public deleteNote(idNote: any) {
    this.loading = true;
    this._notesService.deleteNotesCriticalCases(idNote).pipe(
      finalize(() => {
        this.getInfoNote();
        this.loading = false;
      }) 
    ).subscribe((resp : boolean) => {
      
    });
  }

  public viewCase() {
    this._route.navigateByUrl('');
  }
}
