import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import {environment} from '../../../../environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Headers, RequestOptions, ResponseContentType } from '@angular/http';

@Injectable({
  providedIn: 'root'
})
export class CommentService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiWorkflowUrl;
    this.httpClient = http;
    this.resource = 'Comment/';
  }

  public getCommentsByIdOrder(idOrder: string): Observable<Array<T>> {
    const path = 'getByOrder?idOrder=' + idOrder;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

  public getCommentsByIdActivity(idActivity: string, idOrder: string): Observable<Array<T>> {
    const path = 'getByActivity?idActivity=' + idActivity + '&idOrder=' + idOrder;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

  public downloadCommentFile(body: any): Observable<any> {
    const path = 'download';
    return this.executePost(path, body, null).pipe(
      map((res: any) => res)
    );
  }

}
