import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ServiceService } from '../service.service';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ActivityDocumentService <T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
      super();
      this.apiUrl = environment.apiWorkflowUrl;
      this.httpClient = http;
      this.resource = 'DocumentActivity/';
  }

  public getListActivitiesDocument(idActivity: string): Observable<Array<T>> {
    const path = 'getDocumentsByActivity?idActivity=' + idActivity;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

}
