import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { SubactivityCreditOrder } from '../../models/subactivity-credit-order.model';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class SubactivityCreditOrderService<T> extends ServiceService<SubactivityCreditOrder> {

  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiWorkflowUrl;
    this.httpClient = http;
    this.resource = 'Subactivity/';
  }

  public getSubactivityByActivityId(idActivity: string, idCreditOrder: string): Observable<Array<T>> {
    const path = 'getById?idActivity=' + idActivity + '&idCreditOrder=' + idCreditOrder;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

}
