import { Role } from './role.model.ts';

export class User {
    UserId: number;
    role?: Role;
    UserWeb: string;
    UserName: string;
    Password?: string;
    Active: boolean;
    CreationDate?: Date;
    CreationUser?: string;
    UpdateDate?: Date;
    UpdateUser?: string;
    FirstEntry?: boolean;
    name?: string;

    constructor(model?: User) {
        this.UserId             = model.UserId;
        this.role               = model.role;
        this.UserWeb            = model.UserWeb;
        this.UserName           = model.UserName;
        this.Password           = model.Password;
        this.Active             = (model.Active) ? true : false;
        this.CreationDate       = model.CreationDate;
        this.CreationUser       = model.CreationUser;
        this.UpdateDate         = model.UpdateDate;
        this.UpdateUser         = model.UpdateUser;
        this.FirstEntry         = model.FirstEntry;
        this.name               = model.name;
    }
}
