import { Directive, OnChanges, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { Subject, Subscription, timer } from 'rxjs';
import { switchMap, take, tap } from 'rxjs/operators';

@Directive({
  selector: '[counter]'
})
export class TimerDirective implements OnChanges, OnDestroy {

  private _counterSource$ = new Subject<any>();
  private _subscription = Subscription.EMPTY;

  @Input() counter: number;
  init : number = 0;
  @Input() interval: number;
  @Output() value = new EventEmitter<number>();

  constructor() {
    this._subscription = this._counterSource$.pipe(
      switchMap(({ interval, count, init }) =>
        timer(count, interval).pipe(
          take(count),
          tap(() => this.value.emit(++init))
        )
      )
    ).subscribe();
  }

  ngOnChanges() {
    this._counterSource$.next({ count: this.counter, interval: this.interval, init : 0});
  }

  ngOnDestroy() {
    this._subscription.unsubscribe();
  }

}
