import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpParams } from '@angular/common/http';
import { SalesRegions, GeneralDisbursementsTrending, StagesCases, GeneralCases, PortfolioPurchase, PortfolioPurchaseRunning, LeasingDisburshmentsTrending, InformationCase, Causes } from '../../models/seller-segments.model';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Paginator } from '../../models/paginator.model';

@Injectable({
    providedIn: 'root'
})

export class GeneralDataReportsService extends ServiceService<any> {
    
    constructor(private http: HttpClient) {
        super();
        this.apiUrl = environment.apiReportsUrl;
        this.httpClient = http;
        this.resource = 'GeneralDataReports/';        
    }

    public getSalesRegions() : Observable<SalesRegions[]> {
        const path = `GetSalesRegions`;
        return this.executeGet(path).pipe(
            map((resp : SalesRegions[]) => resp) 
        );
    }

    public getGeneralDisbursementsTrending(idRegion?: number, tipoDesembolso?: String) : Observable<GeneralDisbursementsTrending[]> {
        const path = `GetGeneralDisbursementsTrending`;
        let params = new HttpParams();
        params = params.append('idRegion', String(idRegion));
        params = params.append('tipoDesembolso', String(tipoDesembolso));
        return this.getParams(path, params).pipe(
            map((resp : GeneralDisbursementsTrending[]) => resp) 
        );
    }

    public getStagesCases(dateFrom : Date, dateUntil : Date, codSeller? : number,
        idRegion? : number, idProduct? : number, idSegment? : number) : Observable<StagesCases[]> {
        const path = `GetStagesCases`;
        let params = new HttpParams();
        params = params.append('dateFrom', String(dateFrom));
        params = params.append('dateUntil', String(dateUntil));
        params = params.append('codSeller', String(codSeller));
        params = params.append('idRegion', String(idRegion));
        params = params.append('idProduct', String(idProduct));
        params = params.append('idSegment', String(idSegment));
        return this.getParams(path, params).pipe(
            map((resp : StagesCases[]) => resp) 
        );
    }

    public getGeneralCases(dateFrom : Date, dateUntil : Date, actualPage: number, recordsPerPag: number,codSeller? : number,
        idRegion? : number, idProduct? : number, idSegment? : number) : Observable<Paginator<GeneralCases>> {

        const path = `GetGeneralCases`;
        let params = new HttpParams();
        params = params.append('dateFrom', String(dateFrom));
        params = params.append('dateUntil', String(dateUntil));
        params = params.append('codSeller', String(codSeller));
        params = params.append('idRegion', String(idRegion));
        params = params.append('idProduct', String(idProduct));
        params = params.append('idSegment', String(idSegment));
        params = params.append('numberPage', String(actualPage));
        params = params.append('recordsPerPag', String(recordsPerPag));
        return this.getParams(path, params).pipe(
            map((resp : Paginator<GeneralCases>) => resp) 
        );
    }

    public getPortfolioPurchase(idRegion?: number, codSeller?: number): Observable<PortfolioPurchase[]> {
        const path = `GetPortfolioPurchase`;
        let params = new HttpParams();
        params = params.append('dateFrom', null);
        params = params.append('dateUntil', null);
        params = params.append('codSeller', String(codSeller));
        params = params.append('idRegion', String(idRegion));
        return this.getParams(path, params).pipe(
            map((resp : PortfolioPurchase[]) => resp) 
        );
    }

    public GetPortfolioPurchaseRunning(idRegion?: number): Observable<PortfolioPurchaseRunning[]> {
        const path = `GetPortfolioPurchaseRunning`;
        let params = new HttpParams();
        params = params.append('dateFrom', null);
        params = params.append('dateUntil', null);
        params = params.append('codSeller', null);
        params = params.append('idRegion', String(idRegion));
        return this.getParams(path, params).pipe(
            map((resp : PortfolioPurchaseRunning[]) => resp) 
        );
    }

    public getLeasingDisburshmentsTrending(idRegion?: number): Observable<LeasingDisburshmentsTrending[]> {
        const path = `GetLeasingDisburshmentsTrendingCommand`;
        let params = new HttpParams();

        params = params.append('idRegion', String(idRegion));
        return this.getParams(path, params).pipe(
            map((resp : LeasingDisburshmentsTrending[]) => resp) 
        );
    }

    public GetCodSellerAndRegion(idUser: number): Observable<any> {
        const path = `GetCodSellerAndRegion`;
        let params = new HttpParams();

        params = params.append('idUser', String(idUser));
        return this.getParams(path, params).pipe(
            map((resp : any) => resp) 
        );
    }

    public GetInfomationCase(idCase: number) : Observable<InformationCase> {
        const path = `GetInfomationCase`;
        let params = new HttpParams();

        params = params.append('idFollowCase', String(idCase));
        return this.getParams(path, params).pipe(
            map((resp : InformationCase) => resp) 
        );
    }

    public GetIdOrder(numberCase: string) {
        const path = `GetIdOrder`;
        let params = new HttpParams();

        params = params.append('caseNumber', String(numberCase));
        return this.getParams(path, params).pipe(
            map((resp : number) => resp) 
        );
    }

    public getCauses() : Observable<Causes[]> {
        const path = `GetCauses`;
        return this.executeGet(path).pipe(
            map((resp : Causes[]) => resp) 
        );
    }
}