export class Role {
    RoleId: number;
    RoleName?: string;
    Active?: boolean;
    CreationDate?: Date;
    CreationUser?: string;
    UpdateDate?: Date;
    UpdateUser?: string;
    Module?: any;

    constructor(model?: Role) {
        this.RoleId             = model.RoleId;
        this.RoleName           = model.RoleName;
        this.Active             = (model.Active) ? true : false;
        this.CreationDate       = model.CreationDate;
        this.CreationUser       = model.CreationUser;
        this.UpdateDate         = model.UpdateDate;
        this.UpdateUser         = model.UpdateUser;
        this.Module             = model.Module;
    }
}
