import { Injectable } from '@angular/core';
import { Question } from '../../models/question.model';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class QuestionService<T> extends ServiceService<Question> {

  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiWorkflowUrl;
    this.httpClient = http;
    this.resource = 'Question/';
  }

  public getQuestionByIdActivity(idQuestion: string): Observable<T> {
    const path = 'getByIdActivity?idActivity=' + idQuestion;
    return this.executeGet(path).pipe(
      map((res: T) => res)
    );
  }

}
