  private getStagesCases(idRegion?: number) {
    this.loading = true;
    this._serviceReports.getStagesCases(null, null, null, idRegion, null, null).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : StagesCases[]) => {
      this.stagesCases = resp;
      // console.log(this.stagesCases);
    });
  }


  public getGeneralDisbursementsTrending(idRegion: number) {
    this.loading = true;
    this._serviceReports.getGeneralDisbursementsTrending(idRegion, 'HIPOTECARIO').pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : GeneralDisbursementsTrending[]) => {
      this.generalDisbursementsTrending = resp;
      this.totalDisbursementsAnnual = 0;
        this.totalDisbursementsAntAnnual = 0;
        this.totalDisbursementsMonthly = 0;
        this.totalDisbursementsAntMonthly = 0;
        this.totalDisbursementsDaily = 0;
        this.totalDisbursementsAntDaily = 0;
      this.generalDisbursementsTrending.forEach(element => {
        this.totalDisbursementsAnnual += element.DesembolsoAnioActual;
        this.totalDisbursementsAntAnnual += element.DesembolsoAnioAnterior;
        this.totalDisbursementsMonthly += element.DesembolsoMesActual;
        this.totalDisbursementsAntMonthly += element.DesembolsoMesAnterior;
        this.totalDisbursementsDaily += element.DesembolsoDiaActual;
        this.totalDisbursementsAntDaily += element.DesembolsoDiaAnterior;
        // console.log(element);
      });
    },
    error => {
      this.error = error.error;
    });

    //Leasing
    this._serviceReports.getGeneralDisbursementsTrending(idRegion, 'LEASING').pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : GeneralDisbursementsTrending[]) => {
      this.generalDisbursementsTrendingLeasing = resp;
      this.totalDisbursementsAnnualL = 0;
      this.totalDisbursementsMonthlyL = 0;
      this.generalDisbursementsTrendingLeasing.forEach(element => {
        this.totalDisbursementsAnnualL += element.DesembolsoAnioActual;
        this.totalDisbursementsMonthlyL += element.DesembolsoMesActual;;
      });
    },
    error => {
      this.error = error.error;
    });
  }

