  private getSellerSegments(idRegion?: number) {
    this.loading = true;
    this._service.getSellerSegments(null, null, null, idRegion).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : SellerSegments[]) => {
      this.sellerSegment = resp;
      this.barChartDataSegments = [];
      this.barChartLabelsSegments = [];
      this.barChartDataSegmentsDisbursements = [];
      this.barChartLabelSegmentsDisbursements = [];
      let sellerCases : number[] = [];
      let sellerCasesL : number[] = [];
      let segments = [];
      let segmentsL = [];
      let segmentsDisbursements = [];
      let segmentsDisbursementsL = [];
      this.sellerSegment.filter(element => element.Tipo == 'HIPOTECARIO').forEach(element => {
        segments.push(element.Segmento);
        sellerCases.push(element.Casos);
        segmentsDisbursements.push(element.ValorFinanciar);
      });
      this.sellerSegment.filter(element => element.Tipo == 'LEASING').forEach(element => {
        segmentsL.push(element.Segmento);
        sellerCasesL.push(element.Casos);
        segmentsDisbursementsL.push(element.ValorFinanciar);
      });
      // this.pieChartSegmentsData = sellerCases;
      this.barChartDataSegments = [
        {data: sellerCases, backgroundColor: 'rgba(255, 194, 14, 0.7)', hoverBackgroundColor: 'rgba(255, 194, 14, 1)', yAxisID: 'A'}
      ];
      this.barChartDataSegmentsDisbursements = [
        {data: segmentsDisbursements, backgroundColor: 'rgba(255, 194, 14, 0.7)', hoverBackgroundColor: 'rgba(255, 194, 14, 1)', yAxisID: 'A'}
      ];
      this.barChartDataSegmentsL = [
        {data: sellerCasesL, backgroundColor: 'rgba(38, 34, 97, 0.7)', hoverBackgroundColor: 'rgba(38, 34, 97, 1)', yAxisID: 'A'}
      ];
      this.barChartDataSegmentsDisbursementsL = [
        {data: segmentsDisbursementsL, backgroundColor: 'rgba(38, 34, 97, 0.7)', hoverBackgroundColor: 'rgba(38, 34, 97, 1)', yAxisID: 'A'}
      ];
      this.barChartLabelsSegments = segments;
      this.barChartLabelsSegmentsL = segmentsL;
      // console.log(this.sellerSegment);
    },
    error => {
      this.error = error.error;
    });
  }

  private getDailyDisbursementsTrending(idRegion : number) {
    this.loading = true;
    this._service.getDailyDisbursementsTrending(idRegion).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : DailyDisbursementTrendings[]) => {
      this.dailyDisbursementTrendings = resp;
      // days of current month
      let days: number = new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0).getDate();
      let disbursementsCurrentMonth: number[] = [];
      let disbursementsCurrentMonthLeasing: number[] = [];
      let disbursementsLastMonth: number[] = [];
      let CurrentYear: DailyDisbursementTrendings[] = this.dailyDisbursementTrendings.filter(val => val.year_desemb == new Date().getFullYear() && val.tipo_desembolso == 'HIPOTECARIO');
      let CurrentYearL: DailyDisbursementTrendings[] = this.dailyDisbursementTrendings.filter(val => val.year_desemb == new Date().getFullYear() && val.tipo_desembolso == 'LEASING');
      // let LastYear: DailyDisbursementTrendings[] = this.dailyDisbursementTrendings.filter(val => val.year_desemb == new Date().getFullYear() - 1);
      this.lineChartLabelsDailyDisbursements = [];
      let value1 = 0;
      let value1Leasing = 0;
      let value2 = 0;
      for (let index = 1; index <= days; index++) {
        
        value1 = 0;
        value1Leasing = 0;
        CurrentYear.forEach(element => {
            if(index == element.day_desemb && element.tipo_desembolso == 'HIPOTECARIO') {
              value1 = element.vlr_desembolso;
            }
        });
        CurrentYearL.forEach(element => {
          if(index == element.day_desemb && element.tipo_desembolso == 'LEASING') {
            value1Leasing = element.vlr_desembolso;
          }
      });
        // disbursementsCurrentMonth.push(value1);
        // value2 = 0;
        // LastYear.forEach(element => {
        //   if(index == element.day_desemb) {
        //     value2 = element.vlr_desembolso;
        //   }
        // });
      //  disbursementsLastMonth.push(value2);
       if(!(value1 == 0 && value2 == 0 && value1Leasing == 0)) {
        disbursementsCurrentMonth.push(value1);
        disbursementsLastMonth.push(value2);
        disbursementsCurrentMonthLeasing.push(value1Leasing);
        this.lineChartLabelsDailyDisbursements.push(index.toString());
       }
       
      }
      this.lineChartDataDailyDisbursements = [
        // { data: disbursementsLastMonth, label:`${new Date().getFullYear() - 1} - Valor desembolsado`},
        { data: disbursementsCurrentMonth, label: `Hipotecario` },
        { data: disbursementsCurrentMonthLeasing, label: `Leasing` }
      ];
    },
    error => {
      this.error = error.error;
    });
  }

  private getDisbursementsTrending(idRegion: number):void {
    this.loading = true;
    this._service.getDisbursementsTrending(idRegion).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : DisbursementTrendings[]) => {
      this.disbursementTrendings = resp;
      let disbursementsLastYear :any[12] =  [];
      let disbursementsActualYear :any[12] =  [];
      let disbursementsLeasing: any[12] = [];
      for (let index = 0; index < 12; index++) {
        disbursementsLastYear[index] = 0;
      }
      for (let index = 0; index <= new Date().getMonth(); index++) {
        disbursementsActualYear[index] = 0;
      }
      this.disbursementTrendings.filter(element => element.year_desemb == new Date().getFullYear() - 1 && element.tipo_desembolso == 'HIPOTECARIO').forEach(element => {
        disbursementsLastYear[element.month_desemb - 1] = element.vlr_desembolso;
      });
      this.disbursementTrendings.filter(element => element.year_desemb == new Date().getFullYear() && element.tipo_desembolso == 'HIPOTECARIO').forEach(element => {
        disbursementsActualYear[element.month_desemb - 1] = element.vlr_desembolso; 
        // disbursementsActualYear.push(element.vlr_desembolso);
      });
      this.disbursementTrendings.filter(element => element.year_desemb == new Date().getFullYear() && element.tipo_desembolso == 'LEASING').forEach(element => {
        disbursementsLeasing[element.month_desemb - 1] = element.vlr_desembolso; 
      });
      
      this.lineChartData = [
        { data: disbursementsLastYear, label:`${new Date().getFullYear() - 1}`},
        { data: disbursementsActualYear, label: `${new Date().getFullYear()}` }
      ];
      this.lineChartDataLeasing = [
        { data: disbursementsLeasing, label: `${new Date().getFullYear()}`, backgroundColor: 'rgba(88, 89, 91, 0.8)' }
      ];
    },
    error => {
      this.error = error.error;
    });
  }
