  public message : string;
  public title : string;

  public searchSeller(codSeller: string) : void {
    if (codSeller == '') {
      return;
    }
    this._service.getSellerCases(1, 1,null, null, parseInt(codSeller)).subscribe((resp: any) => {
      if(resp.Result.length <= 0) {
        this.title = 'No hay resultados que coincidan con la busqueda';
        this.message = `No exise el vendedor  no hay casos asociado al cdigo ${codSeller}`;
        $('#defaultModal').modal({
          show : 'true'
        })
      } else {
        this._router.navigate(['/dashboard/reports/details'], { queryParams: {codSeller : codSeller}, skipLocationChange: true });
      }
    });
  }

  public portfolioPurchase: PortfolioPurchase[] = [];
  public portfolioPurchaseRunning: PortfolioPurchaseRunning[] = [];

  public getPortfolioPurchase(idRegion? : number){
    this.loading = true;
    this._serviceReports.getPortfolioPurchase(idRegion).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: PortfolioPurchase[]) => {
      this.portfolioPurchase = resp;
      // console.log(this.portfolioPurchase);
      this.doughnutChartLabels = [];
      this.doughnutChartData = [];
      let data : MultiDataSet = [];
      let casosCurrentYear: number[] = [];
      let desemCurrentYear: number[] = [];
      let casosLastYear: number[] = [];
      let desemLastYear: number[] = [];
      this.portfolioPurchase.filter(element => element.year_desemb == new Date().getFullYear()).forEach(element => {
        // let vlr = `Casos ${element.year_desemb}`;
        casosCurrentYear.push(element.casos);
        desemCurrentYear.push(element.vlr_desembolso);
      });
      this.portfolioPurchase.filter(element => element.year_desemb == new Date().getFullYear() - 1).forEach(element => {
        // let vlr = `Casos ${element.year_desemb}`;
        casosLastYear.push(element.casos);
        desemLastYear.push(element.vlr_desembolso);
      });
      // this.doughnutChartLabels.push('Desembolsos', 'Casos');
      // this.doughnutChartData = data;
      this.doughnutChartData = [
        {data: desemCurrentYear, label: `Desembolsos ${new Date().getFullYear()}`, backgroundColor: 'rgba(255, 194, 14, 0.7)', hoverBackgroundColor: 'rgba(255, 194, 14, 0.8)', yAxisID: 'A'},
        {data: desemLastYear, label: `Desembolsos ${new Date().getFullYear() - 1}`, backgroundColor: 'rgba(255, 194, 14, 0.7)', hoverBackgroundColor: 'rgba(255, 194, 14, 0.8)', yAxisID: 'A'},
        {data: casosCurrentYear, label: `Casos ${new Date().getFullYear()}`, backgroundColor: 'rgba(0, 102, 179, 0.7)', hoverBackgroundColor: 'rgba(0, 102, 179, 0.8)', yAxisID: 'B'},
        {data: casosLastYear, label: `Casos ${new Date().getFullYear() - 1}`, backgroundColor: 'rgba(0, 102, 179, 0.7)', hoverBackgroundColor: 'rgba(0, 102, 179, 0.8)', yAxisID: 'B'}
      ];
    }, error => {
      this.error = error.error;
    });
  }

