  public verDatosRegion(idRegion : number){
    this.getDailyDisbursementsTrending(idRegion);
    this.getGlobalGoals(idRegion);
    this.getDisbursementsCurrentYear(idRegion);
    this.getDisbursementsTrending(idRegion);
    this.getGeneralDisbursementsTrending(idRegion);
    this.getSellerSegments(idRegion);
    this.getPortfolioPurchaseRunning(idRegion);
    this.getSellerProducts(idRegion);
    this.getStagesCases(idRegion);
    this.getPortfolioPurchase(idRegion);
    this.getLeasingDisburshmentsTrending(idRegion);

    $('.btn').removeClass('bg-custom-yellow');
    idRegion ? $('#btnRegion' + idRegion).addClass('bg-custom-yellow') 
      : $('#btnRegionAll').addClass('bg-custom-yellow');
    
  }

