  public getLeasingDisburshmentsTrending(idRegion? : number) {
    this.loading = false;
    this._serviceReports.getLeasingDisburshmentsTrending(idRegion).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: any) => {
      this.leasingDisburshmentsTrending = resp;
      this.casos_Leasing = 0;
      this.desembolsos_Leasing = 0;
      this.leasingDisburshmentsTrending.forEach(element => {
        this.casos_Leasing += element.Casos_Leasing;
        this.desembolsos_Leasing += element.Desembolso;
      });
      // console.log(this.leasingDisburshmentsTrending);
    }, error => {
      this.error = error.error;
    })
  }

  private getRegionUser() {
    this.loading = true;
    this._serviceReports.GetCodSellerAndRegion(GlobalConstants.obtenerIdUsuario())
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: any) => {
      this.idRegionUser = resp['idRegion'];
      this.getGlobalGoals(this.idRegionUser);
      this.getDisbursementsTrending(this.idRegionUser);
      this.getGeneralDisbursementsTrending(this.idRegionUser)
      this.getDailyDisbursementsTrending(this.idRegionUser);
      this.getSellerSegments(this.idRegionUser);
      if(this.idRegionUser == null) {
        this.getSellerRegions(); 
        this.getSalesRegions();
      } 
      this.getPortfolioPurchase(this.idRegionUser);
      this.getSellerProducts(this.idRegionUser);
      this.getStagesCases(this.idRegionUser);
      this.getPortfolioPurchaseRunning(this.idRegionUser);
      this.getLeasingDisburshmentsTrending(this.idRegionUser);
      // console.log(this.idRegionUser);
    },
    error => {
      this.error = error.error;
    });
  }
