import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient, HttpParams } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { GlobalGoals } from '../../models/global-goals.model';
import { DisbursementsCurrentYears } from '../../models/disbursements-current.year.model';
import { DisbursementTrendings, DailyDisbursementTrendings } from '../../models/disbursements-trending';
import { ComplianceDisbursements } from '../../models/compliance-disbursements.model';
import { SellerSegments, SellerCases, SellerRegions, SellerProducts } from '../../models/seller-segments.model';
import { Paginator } from '../../models/paginator.model';

@Injectable({
  providedIn: 'root'
})
export class RealStateReportsService extends ServiceService<any> {
  
  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiReportsUrl;
    this.httpClient = http;
    this.resource = 'RealStateDisbursementsSummary/';
  }

  public getDailyDisbursementsTrending(idRegion? : number) {
    const path = `GetDailyDisbursementsTrending`;
    let params = new HttpParams();
    params = params.append('idRegion', String(idRegion));

    return this.getParams(path, params).pipe(
      map((resp : DailyDisbursementTrendings[]) => resp) 
    );
  }

  public getDisbursementsTrending(idRegion?: number): Observable<DisbursementTrendings[]> {
    const path = `GetDisburshmentsTrending`;
    let params = new HttpParams();
    params = params.append('idRegion', String(idRegion));
    return this.getParams(path, params).pipe(
      map((resp : DisbursementTrendings[]) => resp) 
    );
  }

  public getAGlobalGoals(idRegion?: number): Observable<GlobalGoals> {
    const path = `GetAGlobalGoals`;
    let params = new HttpParams();
    params = params.append('idRegion', String(idRegion));
    return this.getParams(path, params).pipe(
        map((resp : GlobalGoals) => resp) 
    );
  }

  public getDisburshmentsCurrentYear(idRegion?: number): Observable<DisbursementsCurrentYears> {
    const path = `GetDisburshmentsCurrentYear`;
    let params = new HttpParams();
    params = params.append('idRegion', String(idRegion));
    return this.getParams(path, params).pipe(
        map((resp : DisbursementsCurrentYears) => resp) 
    );
  }

  public getDailyComplianceDisbursements(): Observable<ComplianceDisbursements[]> {
    const path = `GetDailyComplianceDisbursements`;
    return this.executeGet(path).pipe(
        map((resp : ComplianceDisbursements[]) => resp) 
    );
  }

  public getMonthlyComplianceDisbursements() : Observable<ComplianceDisbursements[]> {
    const path = `GetMonthlyComplianceDisbursements`;
    return this.executeGet(path).pipe(
        map((resp : ComplianceDisbursements[]) => resp) 
    );
  }

  public getAnnualComplianceDisbursements() : Observable<ComplianceDisbursements[]> {
    const path = `GetAnnualComplianceDisbursements`;
    return this.executeGet(path).pipe(
      map((resp: ComplianceDisbursements[]) => resp)
    );
  }

  public getSellerSegments(dateFrom?: Date, dateUntil?: Date, codSeller?: number, idRegion?: number) : Observable<SellerSegments[]> {
    const path = `GetSellerSegments`;
    let params = new HttpParams();
    params = params.append('dateFrom', String(dateFrom));
    params = params.append('dateUntil', String(dateUntil));
    params = params.append('codSeller', String(codSeller));
    params = params.append('idRegion', String(idRegion));
    return this.getParams(path, params).pipe(
        map((resp : SellerSegments[]) => resp) 
    );
  }

  public getSellerCases(numberPage: number, recordsPerPage: number, 
    dateFrom?: Date, dateUntil?: Date, codSeller?: number,
    ) : Observable<Paginator<SellerCases>> {
    const path = `GetSellerCases`;
    let params = new HttpParams();
    params = params.append('dateFrom', String(dateFrom));
    params = params.append('dateUntil', String(dateUntil));
    params = params.append('codSeller', String(codSeller));
    params = params.append('numberPage', String(numberPage));
    params = params.append('recordsPerPage', String(recordsPerPage));
    return this.getParams(path, params).pipe(
        map((resp : Paginator<SellerCases>) => resp) 
    );
  }

  public getSellerRegions(dateFrom?: Date, dateUntil?: Date, codSeller?: number) : Observable<SellerRegions[]> {
    const path = `GetSellerRegions`;
    let params = new HttpParams();
    params = params.append('dateFrom', String(dateFrom));
    params = params.append('dateUntil', String(dateUntil));
    params = params.append('codSeller', String(codSeller));
    return this.getParams(path, params).pipe(
        map((resp : SellerRegions[]) => resp) 
    );
  }

  public getSellerProducts(dateFrom?: Date, dateUntil?: Date, codSeller?: number, idRegion?: number) : Observable<SellerProducts[]> {
    const path = `GetSellerProducts`;
    let params = new HttpParams();
    params = params.append('dateFrom', String(dateFrom));
    params = params.append('dateUntil', String(dateUntil));
    params = params.append('codSeller', String(codSeller));
    params = params.append('idRegion', String(idRegion));
    return this.getParams(path, params).pipe(
        map((resp : SellerProducts[]) => resp) 
    );
  }
}
