import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotificationsService } from 'src/app/core/services/workFlowModule/notifications.service';
import { GlobalConstants } from '../../constants';
import {Observable} from 'rxjs/Rx';
import { Notification } from 'src/app/core/models/notification.model';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  public idUser: string;
  public notifications: Array<Notification>;

  constructor(private router: Router,
              private notificationService: NotificationsService<Notification>) { }

  ngOnInit() {
    this.getNotification();
    Observable.interval(100000).subscribe(x => {
      this.getNotification();
    });
  }

  public getNotification() {
    this.idUser = GlobalConstants.obtenerIdUsuario();
    this.notificationService.getNotificationByUser(this.idUser).
    subscribe(
      (response: any) => {
        if (response != null) {
          this.notifications = response;
          //this.toast.add({ severity: 'success', detail: 'Se ha actualizado la informacin' });
        }
      },
      error => {
        //this.toast.add({ severity: 'danger', detail: 'Ha ocurrido un error:' + error });
      }
    );
  }

  public cerrarSesion(): void {
    localStorage.removeItem(window.location.hostname);
    this.router.navigateByUrl('/login');
  }

  public notificationEvent(notification: Notification): void {
    notification.Checked = true;
    this.notificationService.update(notification, 'update').subscribe(
      (response: any) => {
        if (response != null) {
          this.router.navigateByUrl('/dashboard/Workflow/schedule/' + notification.OrderId);
          this.getNotification();
        }
      },
      error => {
        //this.toast.add({ severity: 'danger', detail: 'Ha ocurrido un error:' + error });
      }
    );
  }

}
