import { Component, OnInit, Input, CodegenComponentFactoryResolver } from '@angular/core';
import { ClientOrder } from 'src/app/core/models/client-order.model';
import { ClientOrderService } from 'src/app/core/services/workFlowModule/client-order.service';
import { ClientEnsureService } from 'src/app/core/services/workFlowModule/client-ensure.service';
import { ClientEnsure } from 'src/app/core/models/client-ensure.model';
import { UserService } from 'src/app/core/services/userModule/user.service';
import { User } from 'src/app/core/models/user.model';
import { GlobalConstants } from 'src/app/shared/constants';
import { finalize } from 'rxjs/operators';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-modal-clients',
  templateUrl: './modal-clients.component.html',
  styleUrls: ['./modal-clients.component.css']
})
export class ModalClientsComponent implements OnInit {

  @Input()
  public client: ClientOrder;
  @Input()
  public idOrder: number;
  public clientInsured: ClientEnsure;
  public showPerc: string;
  public loading: boolean;
  public clientInsuredForm: FormGroup;

  constructor(private clientOrderService: ClientOrderService<ClientOrder>,
              private toast: MessageService,
              private clientEnsureService: ClientEnsureService<ClientEnsure>,
              private formBuilder: FormBuilder) {
    this.clientInsuredForm = new FormGroup({
      ensureMarker: new FormControl('', [Validators.required]),
      perc: new FormControl('', [Validators.required]),
      clientPhone: new FormControl('', [Validators.required]),
      clientMovil: new FormControl('', [Validators.required]),
      clientEmail: new FormControl('', [Validators.required])
    });
  }

  ngOnInit() {
    this.getClientInsured();
  }

  private getClientInsured() {
    this.clientEnsureService.get(this.client.ClientOrderId + '', 'getClientInsured/').pipe(
      finalize (
        () =>  this.loading = false
      )
    ).subscribe(
      (response: ClientEnsure) => {
        this.clientInsured = response;
        this.fillForm();
      },
      error => {
        console.log(error.type);
      }
    );
  }

  private onSelectedAssurance(event: any) {
    this.showPerc = event;
  }

  private fillForm() {
    if (this.clientInsured != null) {
      this.showPerc = this.clientInsured.ContributorMarking ? 'true' : '';
      this.clientInsuredForm = new FormGroup({
        ensureMarker: new FormControl(this.clientInsured.ContributorMarking, [Validators.required]),
        perc: new FormControl(this.clientInsured.Percentage, [Validators.required]),
        clientPhone: new FormControl(this.client.ClientPhone, [Validators.required]),
        clientMovil: new FormControl(this.client.ClientMovil, [Validators.required]),
        clientEmail: new FormControl(this.client.ClientEmail, [Validators.required])
      });
    }
  }

  private mapData() {
    if (this.clientInsured == null) {
      this.clientInsured = new ClientEnsure();
    }
    this.clientInsured.CreditOrderId = this.idOrder;
    this.clientInsured.ContributorMarking = this.clientInsuredForm.get('ensureMarker').value;
    this.clientInsured.ClientOrderId = this.client.ClientOrderId;
    this.clientInsured.Percentage = this.clientInsuredForm.get('perc').value;
    this.clientInsured.CreationDate = new Date().toString();
    this.clientInsured.CreationUser = GlobalConstants.obtenerNombreUsuario();
    this.clientInsured.UpdatedDate = new Date().toString();
    this.clientInsured.UpdatedUser = GlobalConstants.obtenerNombreUsuario();
    this.client.ClientPhone = this.clientInsuredForm.get('clientPhone').value;
    this.client.ClientMovil = this.clientInsuredForm.get('clientMovil').value;
    this.client.ClientEmail = this.clientInsuredForm.get('clientEmail').value;
    this.client.IdOrder = this.idOrder;
  }

  private onSubmit() {
    if (this.validateFormData()) {
      this.mapData();
      if (this.clientInsured.ClientInsuredId > 0) {
        this.update();
      } else {
        this.save();
      }
    }
  }

  private validateFormData() {
    if ((this.clientInsuredForm.get('ensureMarker').value &&
    this.clientInsuredForm.get('perc').value !== '') || !this.clientInsuredForm.get('ensureMarker').value) {
      return true;
    }
  }

  private save() {
    this.clientEnsureService.postClientInsured(this.clientInsured).pipe(
      finalize (
        () =>  this.loading = false
      )
    ).subscribe(
      (response: ClientEnsure) => {
        this.clientInsured = response;
        this.updateContactData();
        this.toast.add({ severity: 'success', detail: 'Se ha guardado la informacin' });
      },
      error => {
        this.toast.add({ severity: 'danger', detail: 'Ha ocurrido un error:' + error });
      }
    );
  }

  private update() {
    this.clientEnsureService.update(this.clientInsured, 'putClientInsured').pipe(
      finalize (
        () =>  this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.clientInsured = response;
          this.updateContactData();
          this.toast.add({ severity: 'success', detail: 'Se ha guardado la informacin' });
        }
      },
      error => {
        this.toast.add({ severity: 'danger', detail: 'Ha ocurrido un error:' + error });
      }
    );
  }

  private updateContactData() {
    this.clientOrderService.update(this.client, 'putOtherMembers').pipe(
      finalize (
        () =>  this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.client = response;
        }
      },
      error => {
        this.toast.add({ severity: 'danger', detail: 'Ha ocurrido un error:' + error });
      }
    );
  }

}
