import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/core/models/user.model';
import { UserService } from 'src/app/core/services/userModule/user.service';
import { GlobalConstants } from 'src/app/shared/constants';
import { OptionRole } from 'src/app/core/models/optionRole';
import { finalize } from 'rxjs/operators';
import { RoleService } from 'src/app/core/services/userModule/role.service';
import { Role } from 'src/app/core/models/role.model.ts';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ChangePasswordService } from 'src/app/core/services/userModule/changePassword.service';
import { MessageService } from 'primeng/api';
import { Router } from '@angular/router';

@Component({
  selector: 'app-profile-change-password',
  templateUrl: './profile-change-password.component.html',
  styleUrls: ['./profile-change-password.component.css']
})
export class ProfileChangePasswordComponent implements OnInit {

  public user: User;
  public role: Role;
  public loading: boolean;
  public idUser: string;
  public idRole: string;
  public formGroup: FormGroup;
  public validateFieldsRequired: boolean;
  public claveActual: string;
  public claveNueva: string;
  public claveConfirmar: string;
  public passwordValidate: string;

  private login: any;

  constructor(private userService: UserService<User>,
              private roleService: RoleService<Role>,
              private changePasswordService: ChangePasswordService<any>,
              private toast: MessageService,
              private router: Router, ) {
    this.formGroup = new FormGroup({
      claveActual: new FormControl('', Validators.required),
      claveNueva: new FormControl('', Validators.required),
      claveConfirmar: new FormControl('', Validators.required)
    });
  }

  ngOnInit() {
    this.loadInformation();
  }

  private loadInformation(): void {
    this.loading = true;
    this.idUser = GlobalConstants.obtenerIdUsuario();
    this.userService.getUserInfo(this.idUser).pipe(
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (user: User) => {
        if (user != null) {
          this.user = user;
          this.idRole = this.user.role.RoleId + '';
          this.getRole();
        }
    });
  }

  public getRole() {
    this.loading = true;
    this.roleService.get(this.idRole, 'getRole/').pipe(
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (role: Role) => {
        if (role != null) {
          this.role = role;
        }
    });
  }

  public changePassword(): void {
    this.validateFieldsRequired = true;

    if (this.formGroup.valid) {
      this.validateFieldsRequired = false;
      if (this.formGroup.get('claveActual').value !== this.formGroup.get('claveNueva').value) {
        if (this.formGroup.get('claveNueva').value === this.formGroup.get('claveConfirmar').value) {
          this.user.Password = this.formGroup.get('claveNueva').value;
          this.finalChange(this.user);
        } else {
            this.toast.add({ severity: 'warn', detail: 'Los campos contrasea nueva y confirmacin nueva contrasea, no coinciden' });
        }
      } else {
        this.toast.add({severity: 'warn', detail: 'La contrasea actual no pueder ser igual a la contrasea nueva' });
        this.loading = false;
      }
    }
  }

  public validarControles(nombreControl: string): boolean {
    return this.validateFieldsRequired && this.retornarControl(nombreControl).hasError('required');
  }

  public retornarControl(nombreControl: string): any {
    return this.formGroup.get(nombreControl);
  }

  private finalChange(user: User): void {
    this.loading = true;
    if (this.login !== undefined && this.login != null) {
      user.UserName = this.login;
    }
    this.changePasswordService.changePassword(user).subscribe(
      (resultado: boolean) => {
        if (resultado) {
          this.toast.add({severity: 'success', detail: 'Contrasea cambiada correctamente' });
          this.cleanFields();
        } else {
          this.toast.add({severity: 'error', detail: 'La contrasea no se ha podido actualizar' });
        }
        this.loading = false;
      },
      (error) => {
        this.toast.add({severity: 'error', detail: 'La contrasea no se ha podido actualizar' });
        this.loading = false;
      });
  }

  public onKey(evento: any, control: string) {
    if (evento['key'] !== ' ') {
      this.passwordValidate = this.formGroup.get(control).value;
    } else {
      this.passwordValidate = this.passwordValidate.replace(' ', '');  // this.passwordValidate.trimRight();
    }
    this.formGroup.controls[control].setValue(this.passwordValidate);
  }

  public cleanFields(): void {
    this.formGroup.get('claveActual').setValue('');
    this.formGroup.get('claveNueva').setValue('');
    this.formGroup.get('claveConfirmar').setValue('');
  }

}
