import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { GlobalConstants } from '../../../shared/constants';
import { ServiceService } from '../service.service';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { User } from '../../models/user.model';

@Injectable()
export class ChangePasswordService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiUserUrl;
  }

  public changePassword(user: User): Observable<any> {
    let token: string  = localStorage.getItem('token_tmp');

    if (token !== undefined && token !== null) {
        token = GlobalConstants.encryptor.get(GlobalConstants.claveEncriptacion, token);
        return this.http.put<boolean>(this.apiUrl + `User/ChangePassword`, user, {
            headers: new HttpHeaders(
            {
                Authorization: 'Bearer ' + token
            })
        });
    } else {
        return this.http.put<boolean>(this.apiUrl + `User/ChangePassword`, user);
    }
  }

}
