import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { CreditOrderActivity } from '../../models/credit-order-activity.model';
import { environment } from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CreditOrderActivityService<T> extends ServiceService<CreditOrderActivity> {

  constructor(private http: HttpClient) {
    super();
    this.apiUrl = environment.apiWorkflowUrl;
    this.httpClient = http;
    this.resource = 'CreditOrderActivity/';
  }

  public getActivitiesByIdOrder(idOrder: string): Observable<Array<T>> {
    const path = 'getAll?idCreditOrder=' + idOrder;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

}
