import { Component, OnInit, Input } from '@angular/core';
import { CreditOrderActivity } from 'src/app/core/models/credit-order-activity.model';
import { CreditOrderActivityService } from 'src/app/core/services/workFlowModule/credit-order-activity.service';
import { finalize } from 'rxjs/operators';
import { Process } from 'src/app/core/models/process.model';
import { FormGroup, FormControl } from '@angular/forms';
import { Comment } from 'src/app/core/models/comment.model';
import { CommentService } from 'src/app/core/services/workFlowModule/comment.service';

@Component({
  selector: 'app-activities-board',
  templateUrl: './activities-board.component.html',
  styleUrls: ['./activities-board.component.css']
})
export class ActivitiesBoardComponent implements OnInit {

  // Data variables
  @Input()
  public idOrder: string;
  public creditActivities: Array<CreditOrderActivity>;
  public comments: Array<Comment> = [];
  public process: Array<Process> = [];
  public error: string;
  public loading = false;

  constructor(private creditActivityService: CreditOrderActivityService<CreditOrderActivity>) {}

  ngOnInit() {
   this.getActivities();
  }
 

  private getActivities() {
    this.loading = true;
    this.creditActivityService.getActivitiesByIdOrder(this.idOrder).pipe (
      finalize (
        () => this.loading = false
      )
    ).subscribe((activities: Array<CreditOrderActivity>) => {
        this.creditActivities = activities;
        this.getProcesos();
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private getProcesos() {
    let aux1 = '';
    let aux2 = '';
    let pro = new Process();
    const proc: Process[] = [];
    this.creditActivities.forEach((creditOrderActivity: CreditOrderActivity) => {
      aux2 = creditOrderActivity.ProcessName;
      pro = new Process();
      if (aux1 !== aux2) {
        pro.idProcess = creditOrderActivity.IdProccess;
        pro.processName = aux2;
        proc.push(pro);
        aux1 = aux2;
      }
    });
    this.process = proc;
  }

  onReloadEvent(reload: boolean) {
    console.log(reload);
    this.getActivities();
  }

  getActivityName(activity: CreditOrderActivity) {
    const provider = activity.Provider;
    const client = activity.ClientOrder;
    let name = activity.ActivityName;
    if (provider !== null && provider !== undefined) {
      name += ' - ' + provider.ProviderName;
    } else if (client !== null && client !== undefined) {
      name += ' - ' + client.ClientName;
    }
    return name;
  }

}
