import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { LoginService } from '../../core/services/securityModule/login.service';
import { Encryptor } from '../../shared/encryptor';
import { GlobalConstants } from '../../shared/constants';
import { Router } from '@angular/router';
import { finalize } from 'rxjs/operators';

import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public loginForm: FormGroup;
  public validateRequiredFields: boolean;
  public loading: boolean;
  public message: string;

  constructor(
    private loginService: LoginService<any>,
    private router: Router,
    private encryptor: Encryptor,
    private toast: MessageService
    ) {
      this.loginForm = new FormGroup ({
        user: new FormControl('', [Validators.required]),
        password: new FormControl('', [Validators.required]),
      });
      this.loading = false;
      this.message = '';
  }

  ngOnInit() { }

  public signIn(): void {
    this.loading = true;
    this.loginService.authenticate(this.loginForm.get('user').value, this.loginForm.get('password').value).pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe((token: any) => {
          const encryption = this.encryptor.set(GlobalConstants.claveEncriptacion, token.access_token);
          localStorage.removeItem(window.location.hostname);
          localStorage.setItem(window.location.hostname, encryption);

          // Validacin de ingreso por primera vez
          if (GlobalConstants.validarPrimeraVez(token.access_token)) {
            console.log("Primera vez entrando");
            localStorage.setItem('token_tmp', encryption);
            const login: string = btoa(this.loginForm.get('user').value);
            this.router.navigate(['/ChangePassword'], {queryParams: { loginT: login }});

          } else {
            this.router.navigateByUrl('/dashboard/Workflow/schedule');
            this.message = '';
          }
      },
      (error) => {
        this.loading = false;
        this.toast.add({severity: 'error', summary: '',
          detail: 'Se ha presentado un error al tratar de ingresar al sistema, verifique usuario y contrasea.'});
      }
    );
  }

}
