import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from '../shared/dashboard/dashboard.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { CreditInformationComponent } from './credit-information/credit-information.component';
import { NotFoundComponent } from '../shared/components/not-found/not-found.component';
import { HomeComponent } from './home/home.component';
import { ChangePasswordComponent } from '../security/ChangePassword/ChangePassword.component';

const workflowRoutes: Routes = [

    {path: 'schedule', component : ScheduleComponent},
    {path: 'schedule/:idOrder', component: CreditInformationComponent}
];

@NgModule({
    imports: [
        RouterModule.forChild(workflowRoutes)
    ],
    exports: [
        RouterModule
    ]
})
export class WorkFlowRoutingModule {

}
