import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { Comment } from 'src/app/core/models/comment.model';
import { CommentService } from 'src/app/core/services/workFlowModule/comment.service';
import { GlobalConstants } from 'src/app/shared/constants';
import { CreditDocument } from 'src/app/core/models/credit-document';
import { CreditDocumentService } from 'src/app/core/services/workFlowModule/credit-document.service';
import { MessageService } from 'primeng/api';
import { CreditOrderActivity } from 'src/app/core/models/credit-order-activity.model';
import { ActivityDocumentService } from 'src/app/core/services/workFlowModule/activity-document.service';
import { ActivityDocument } from 'src/app/core/models/activity-document.model';

declare var $: any;

@Component({
  selector: 'app-modal-comments',
  templateUrl: './modal-comments.component.html',
  styleUrls: ['./modal-comments.component.css']
})
export class ModalCommentsComponent implements OnInit {

  @Input()
  public activity: CreditOrderActivity;
  @Output()
  public reload = new EventEmitter<boolean>();
  @Input()
  public idOrder: number;

  public fileName: string;
  public commentForm: FormGroup;
  public activityDocument: Array<ActivityDocument>;
  // Control class style
  public classRequiredComment: string;
  public classRequiredState: string;
  public classRequiredFile: string;
  public classRequiredDate: string;

  public fileData: File = null;
  // View controls
  public loading = false;
  public result: string;
  public showDateField: boolean;
  public previewUrl: any = null;
  public error: string;
  public advice: string;

  public dataDismiss: string;

  constructor(private commentService: CommentService<Comment>,
              private creditDocumentService: CreditDocumentService<CreditDocument>,
              private activityDocumentService: ActivityDocumentService<ActivityDocument>,
              private toast: MessageService ) {
    this.commentForm = new FormGroup({
      comment: new FormControl (''),
      state: new FormControl (''),
      file: new FormControl ('')
    });
    this.classRequiredComment = 'warning';
    this.classRequiredState = 'warning';
    this.classRequiredFile = 'primary';
    this.classRequiredDate = 'warning';
   }

  ngOnInit() {
    this.getActivityDocument();
  }

  public getActivityDocument() {
    this.loading = true;
    const idActivity = this.activity.IdActivity + '';
    this.activityDocumentService.getListActivitiesDocument(idActivity).pipe(
      finalize (
        () => this.loading = false
      )).subscribe(
        (response: Array<ActivityDocument>) => {
          if (response != null) {
            this.activityDocument = response;
            this.showDateField = true;
            this.handleActiveDocumentDate();
            this.setDocumentCreditActivity();
          }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private setDocumentCreditActivity() {
    for (const activityDoc of this.activityDocument) {
      this.getCreditDocument(activityDoc);
    }
  }

  private getCreditDocument(activityDocument: ActivityDocument) {
    const idDocument = activityDocument.idDocument + '';
    const idCreditActivity = this.activity.IdCreditActivity + '';
    this.loading = true;
    this.creditDocumentService.getCreditDocument(idCreditActivity, this.idOrder + '', idDocument).pipe(
      finalize (
        () => this.loading = false
      )).subscribe(
        (response: CreditDocument) => {
          if (response != null) {
            activityDocument.creditDocument = response;
          }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private handleActiveDocumentDate() {
    if (this.activityDocument != null) {
      for (const doc of this.activityDocument) {
        const fc = new FormControl('');
        this.commentForm.addControl('documentDate' + doc.idDocument, fc);
        this.commentForm.addControl('idRoute' + doc.idDocument, fc);
      }
    }
  }

  private handleRemoveDocumentDate() {
    if (this.activityDocument != null) {
      for (const doc of this.activityDocument) {
        this.commentForm.removeControl('documentDate' + doc.idDocument);
        this.commentForm.removeControl('idRoute' + doc.idDocument);
      }
    }
  }

  public onSubmit() {
    const formGroup = this.commentForm;
    if (formGroup.get('comment').value !== '' && formGroup.get('state').value !== '' ) {
      const orderOut = 0;
      const newClass = {
        comment: formGroup.get('comment').value,
        user: GlobalConstants.obtenerNombreUsuario(),
        idCurrentState: formGroup.get('state').value,
        idPreviousState: this.activity.CurrentStateId,
        idActivity: this.activity.IdActivity,
        idOrder: this.idOrder} as Comment;
      this.saveComment(newClass);
      if (this.showDateField && this.activityDocument != null) {
        for (const activityDoc of this.activityDocument) {
          let creditDocument = activityDoc.creditDocument;
          if (creditDocument != null) {
            creditDocument.DocumentDate = formGroup.get('documentDate' + activityDoc.idDocument).value;
            creditDocument.IdRoute = formGroup.get('idRoute' + activityDoc.idDocument).value;
            creditDocument.UpdatedUser = GlobalConstants.obtenerNombreUsuario();
            this.updateDocumentDate(creditDocument);
          } else {
            creditDocument = new CreditDocument();
            creditDocument.CreditOrderId = this.idOrder;
            creditDocument.CreditOrderActivityId = this.activity.IdCreditActivity;
            creditDocument.DocumentId = activityDoc.idDocument;
            creditDocument.DocumentDate = formGroup.get('documentDate' + activityDoc.idDocument).value;
            creditDocument.IdRoute = formGroup.get('idRoute' + activityDoc.idDocument).value;
            creditDocument.CreationUser = GlobalConstants.obtenerNombreUsuario();
            this.saveDocumentDate(creditDocument);
          }
        }
      }
      const idModal: string = '#' + this.activity.IdActivity + 'modal';
      $(idModal).modal('hide');
    } else {
      this.classRequiredComment = formGroup.get('comment').value !== '' ? 'success' : 'error';
      this.classRequiredState = formGroup.get('state').value !== '' ? 'success' : 'error';
    }
  }

  public onFileChange(newObj: any) {
    if (newObj !== null) {
      this.classRequiredFile = 'primary';
      const filename = newObj.replace(/^.*[\\\/]/, '');
      this.fileName = filename;
    }
  }

  public fileProgress(fileInput: any) {
    this.fileData = fileInput.target.files[0] as File;
    console.log(this.fileData);
    this.preview();
  }

  private saveComment(data: any) {
    this.loading = true;
    const formData = new FormData();
    for (const key of Object.keys(data)) {
      formData.append(key, data[key]);
    }
    formData.append('file', this.fileData);
    this.commentService.post(formData, 'save').pipe(
      finalize (
        () => this.loading = false
      )).subscribe((response: any) => {
        if (response != null) {
          this.toast.add({ severity: 'warning', detail: 'Registro exitoso' });
          this.reset();
          this.reload.next(true);
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public updateDocumentDate(document: any) {
    this.loading = true;
    this.creditDocumentService.update(document, 'putSaveDate').pipe(
      finalize (
        () => this.loading = false
      )).subscribe((response: any) => {
        if (response != null) {
          document = response;
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public saveDocumentDate(document: any) {
    this.loading = true;
    this.creditDocumentService.post(document, 'postSaveDate').pipe(
      finalize (
        () => this.loading = false
      )).subscribe((response: any) => {
        if (response != null) {
          document = response;
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public reset() {
    this.commentForm.reset();
    this.fileName = '';
    this.previewUrl = '';
  }

  public preview() {
    // Show preview
    this.loading = true;
    const mimeType = this.fileData.type;
    if (mimeType.match(/image\/*/) == null) {
      this.previewUrl = '';
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(this.fileData);
    reader.onload = (event: any) => {
      this.loading = false;
      this.previewUrl = reader.result;
    };
    console.log(this.previewUrl);
  }

  public onSelectState(event: any) {
    for (const state of this.activity.States) {
      this.advice = '';
      if (state.IdState == event) {
        if (state.StateName === 'Completado' && this.activityDocument !== null) {
          this.showDateField = true;
          this.handleActiveDocumentDate();
        } else {
          this.showDateField = false;
          this.handleRemoveDocumentDate();
        }
        this.advice = state.Advice;
        break;
      }
    }
  }

}
