import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CreditDocumentService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
      super();
      this.apiUrl = environment.apiWorkflowUrl;
      this.httpClient = http;
      this.resource = 'CreditOrderDocument/';
  }

  public getCreditDocument(idActivity: string, idOrder: string, idDocument: string): Observable<T> {
    const path = 'getCreditDocumentsByFilters?idCreditActivity=' + idActivity + '&idOrder=' + idOrder + '&idDocument=' + idDocument;
    return this.executeGet(path).pipe(
      map((res: T) => res)
    );
  }

}
