import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import {environment} from '../../../../environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ClientOrderService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
      super();
      this.apiUrl = environment.apiWorkflowUrl;
      this.httpClient = http;
      this.resource = 'ClientOrder/';
  }

  public getClientsByFiledNumber(filedNumber: string): Observable<Array<T>> {
    const path = 'getClientOrderList?filedNumber=' + filedNumber;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

  public getClientByIdOrder(idOrder: string): Observable<T> {
    const path = 'getById?idOrder=' + idOrder;
    return this.executeGet(path).pipe(
      map((res: T) => res)
    );
  }

}
