import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeReportsComponent } from './home-reports/home-reports.component';
import { DashboardComponent } from '../shared/dashboard/dashboard.component';
import { SellerReportsDetailComponent } from './seller-reports-detail/seller-reports-detail.component';
import { AdminReportsComponent } from './admin-reports/admin-reports.component';
import { CaseDetailsComponent } from './case-details/case-details.component';

const reportsRoutes: Routes = [
    {
        path: 'homeReports',
        component : HomeReportsComponent
    },
    {
        path: 'details',
        component : SellerReportsDetailComponent
    },
    {
        path: 'admin',
        component: AdminReportsComponent
    },
    {
        path: 'caseDetails',
        component: CaseDetailsComponent
    }
];

@NgModule({
    imports: [
        RouterModule.forChild(reportsRoutes)
    ],
    exports: [
        RouterModule
    ]
})
export class ReportsRoutingModule {

}
