import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class NotificationsService<T> extends ServiceService<T> {

  constructor(private http: HttpClient) {
      super();
      this.apiUrl = environment.apiWorkflowUrl;
      this.httpClient = http;
      this.resource = 'Notification/';
  }

  public getNotificationByUser(idUser: string): Observable<T> {
    const path = 'getNotificationsUser?idUser=' + idUser;
    return this.executeGet(path).pipe(
      map((res: T) => res)
    );
  }
}
