import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ServiceService } from '../service.service';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { OptionRole } from '../../models/optionRole';

@Injectable()
export class UserService<T> extends ServiceService<T> {

  constructor(http: HttpClient) {
    super();
    this.apiUrl = environment.apiUserUrl;
    this.httpClient = http;
    this.resource = 'User/';
  }

  //#region Mtodos pblicos
  public getUserInfo(usuario: string): Observable<any> {
    const path = 'getInfo/' + usuario;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

  public getModulesUser(usuario: string): Observable<any> {
    const path = 'getModules/' + usuario;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }
  //#endregion

}
