import { Injectable } from '@angular/core';
import { ServiceService } from '../service.service';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ProvidersService<T> extends ServiceService<T> {

  constructor(http: HttpClient) {
    super();
    this.apiUrl = environment.apiWorkflowUrl;
    this.httpClient = http;
    this.resource = 'Provider/';
  }

  public getProvidersByIdOrder(idOrder: string): Observable<Array<T>> {
    const path = 'getProvidersByOrder?idOrder=' + idOrder;
    return this.executeGet(path).pipe(
      map((res: Array<T>) => res)
    );
  }

}
