import { Component, OnInit, Input } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { CommentService } from 'src/app/core/services/workFlowModule/comment.service';
import { HttpEventType } from '@angular/common/http';

@Component({
  selector: 'app-comments-list',
  templateUrl: './comments-list.component.html',
  styleUrls: ['./comments-list.component.css']
})
export class CommentsListComponent implements OnInit {

  @Input()
  public idActivity: string;
  @Input()
  public idOrder: string;
  public loading: boolean;
  public comments: Array<Comment> = [];
  public error: string;
  private previewUrl: any = null;

  constructor(private commentService: CommentService<Comment>) {
    // Inicializacin de variables de pgina
    this.loading = false;
    this.idOrder = '';
    this.idActivity = '';
  }

  ngOnInit() {
    if (this.idActivity !== '') {
      this.getCommentsByIdActivity();
    } else {
      this.getComments();
    }
  }

  private getComments() {
    this.loading = true;
    this.commentService.getCommentsByIdOrder(this.idOrder).pipe(
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (comments: Array<Comment>) => {
        if (comments != null) {
          this.comments = comments;
        }
      },
      error => {
        this.comments = [];
        this.error = error.error;
      }
    );
  }

  private getCommentsByIdActivity() {
    this.loading = true;
    this.commentService.getCommentsByIdActivity(this.idActivity, this.idOrder).pipe (
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (comments: Array<Comment>) => {
        if (comments != null) {
          this.comments = comments;
        }
      },
      error => {
        this.comments = [];
        this.error = error.error;
      }
    );
  }

  private downloadFile(filePath: string) {
    this.loading = true;
    const formData = new FormData();
    formData.append('filePath', filePath);
    this.commentService.downloadCommentFile(formData).pipe (
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (file) => {
          const byteArray = new Uint8Array(atob(file.Stream).split('').map(char => char.charCodeAt(0)));
          const blob = new Blob([byteArray], { type: file.Type }); // you can change the type
          const url = window.URL.createObjectURL(blob);
          const anchor = document.createElement('a');
          anchor.download = file.FileName;
          anchor.href = url;
          anchor.click();
      },
      error => {
        console.log(error);
      }
    );
  }

}
