import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Question } from 'src/app/core/models/question.model';
import { QuestionService } from 'src/app/core/services/workFlowModule/question.service';
import { Answer } from 'src/app/core/models/answer.model';
import { finalize } from 'rxjs/operators';
import { Router } from '@angular/router';
import { CreditOrderActivity } from 'src/app/core/models/credit-order-activity.model';

@Component({
  selector: 'app-modal-question',
  templateUrl: './modal-question.component.html',
  styleUrls: ['./modal-question.component.css']
})
export class ModalQuestionComponent implements OnInit {

  @Input()
  public activityName: string;
  @Input()
  public activity: CreditOrderActivity;
  @Input()
  public idOrder: number;
  @Output()
  public reload = new EventEmitter<boolean>();
  public loading: boolean;
  public error: string;
  public question: Question;

  constructor(private questionService: QuestionService<Question>,
              private router: Router) {
    this.loading = false;
    this.reload.next(false);
  }

  ngOnInit() {
    this.getQuestionByActivity();
  }

  private getQuestionByActivity() {
    this.questionService.getQuestionByIdActivity(this.activity.IdActivity + '').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: Question) => {
        if (response != null) {
          this.question = response;
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private onClick(idAnswer, idQuestion) {
    const answer = {
      IdAnswer: idAnswer,
      IdQuestion: idQuestion,
      AnswerOption: '',
      IdOrder : this.idOrder,
      IdActivity : this.activity.IdActivity,
      IdClient: this.activity.ClientOrder != null ? this.activity.ClientOrder.ClientOrderId : null,
      IdProvider: this.activity.Provider != null ? this.activity.Provider.ProviderId : null,
      User : 'Javier',
    } as Answer;
    this.questionService.post(answer, 'save').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response === 1) {
          this.reload.next(true);
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  public navigate() {
    this.router.navigateByUrl('/.');
  }

}
