import { Component, OnInit } from '@angular/core';
import { GeneralCases, NotesCriticalCases, Causes } from 'src/app/core/models/seller-segments.model';
import { FormGroup, FormControl } from '@angular/forms';
import { GeneralDataReportsService } from 'src/app/core/services/workFlowModule/general-data-reports.service';
import { NotesCriticalCasesService } from 'src/app/core/services/reports/notes-critical-cases.service';
import { finalize } from 'rxjs/operators';
import { Paginator } from 'src/app/core/models/paginator.model';
import { GlobalConstants } from 'src/app/shared/constants';

declare const $: any;

@Component({
  selector: 'app-admin-reports',
  templateUrl: './admin-reports.component.html',
  styleUrls: ['./admin-reports.component.css']
})
export class AdminReportsComponent implements OnInit {

  public generalCases: Paginator<GeneralCases>;
  public loading: boolean = false;
  public classRequiredComment = 'warning';
  public classRequiredState = 'warning';
  public classRequiredFile = 'primary';

  public commentForm: FormGroup;
  public actualPage : number = 1;
  public totalCases : number;
  public idRegionUser: number;
  public error: any;
  public causes: Causes[] = [];
  public btnSaveOrUpdate = 'GUARDAR CAMBIOS';

  constructor(private _serviceReports : GeneralDataReportsService, 
    private _notesService: NotesCriticalCasesService) { 
    this.commentForm = new FormGroup({
      description: new FormControl (''),
      // sendTo: new FormControl (''),
      causal : new FormControl(''),
      idCase : new FormControl(''),
      idNote : new FormControl(''),
    });
    this.generalCases = new Paginator();
    this.getCauses()
  }

  ngOnInit() {
    this.getRegionUser();
    this.classRequiredComment = 'warning';
    this.classRequiredState = 'warning';
    this.classRequiredFile = 'primary';
  }


  public getCauses() {
    this._serviceReports.getCauses()
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: Causes[]) => {
      this.causes = resp;
    },
    error => {
      this.error = error.error;
    });
  }

  private getRegionUser() {
    this.loading = true;
    this._serviceReports.GetCodSellerAndRegion(GlobalConstants.obtenerIdUsuario())
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: any) => {
      this.idRegionUser = resp['idRegion'];
      this.getGeneralCases();
      // console.log(this.idRegionUser);
    },
    error => {
      this.error = error.error;
    });
  }
  

  public getGeneralCases() {
    this.loading = true;
    this._serviceReports.getGeneralCases(null, null,this.actualPage, 10, null, this.idRegionUser, null, null).pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp : Paginator<GeneralCases>) => {
      this.generalCases = resp;
      this.totalCases = this.generalCases.TotalRecords;
      // console.log(this.pages)
      // console.log(this.generalCases);
    });
  }
  
  public manageCase(item: GeneralCases) {
    this.getInfoNote(item);
    // console.log(item.Id_Seguimiento);
    // console.log(item.Estado);
    // if(item.Estado) {
    //   this._notesService.getNotesCriticalCasesByCase(item.Id_Seguimiento).subscribe((resp : NotesCriticalCases) => {
    //     this.commentForm.setValue(
    //       {
    //         idCase : item.Id_Seguimiento, 
    //         description: resp.Comentarios, 
    //         sendTo: resp.AtribuibleA, 
    //         causal: resp.Id_Causal,
    //         idNote : resp.Id_Nota
    //       });
    //   })
    // } else{
    //   this.commentForm.setValue({idCase : item.Id_Seguimiento, description: '', sendTo: '', causal: '', idNote: 0});
    // }
    
    // $('#numberCase').text(item.tracking_numero_radicado);
    // $('#ManageCasetModal').modal({
    //   show: 'true'
    // });
  }

  public onSubmit(formComment: any) {
    if (formComment.description !== '' && formComment.causal !== '0' && formComment.causal !== '') {
      let model: NotesCriticalCases = {
        Id_Caso: formComment.idCase,
        Comentarios: formComment.description,
        Id_Causal: formComment.causal,
        AtribuibleA: null,
        Id_Usuario: GlobalConstants.obtenerIdUsuario(),
        Id_Nota: formComment.idNote
      };
        // console.log(model);
      if(formComment.idNote == '' || formComment.idNote === 0) {
        this.saveComment(model);
      } else {
        this.updateComment(model)
      }
      
      const idModal: string = '#ManageCasetModal';
      $(idModal).modal('hide');
      this.getGeneralCases();
    } else {
      this.classRequiredComment = formComment.description !== '' ? 'success' : 'error';
      // this.classRequiredState = formComment.sendTo !== '' ? 'success' : 'error';
      this.classRequiredState = formComment.causal !== '0' && formComment.causal !== '' ? 'success' : 'error';
    }
  }

  public getInfoNote(item: GeneralCases) {
    // console.log(item);
    if(item.Estado) {
      this._notesService.getNotesCriticalCasesByCase(item.Id_Seguimiento).subscribe((resp : NotesCriticalCases) => {
        this.commentForm.setValue(
          {
            idCase : item.Id_Seguimiento, 
            description: resp.Comentarios, 
            // sendTo: resp.AtribuibleA, 
            causal: resp.Id_Causal,
            idNote : resp.Id_Nota
          });
          this.btnSaveOrUpdate = 'ACTUALIZAR CAMBIOS';
      })
    } else{
      this.commentForm.setValue({idCase : item.Id_Seguimiento, description: '', causal: 0, idNote: 0});
      this.btnSaveOrUpdate = 'GUARDAR CAMBIOS';
    }
    // const idModal: string = '#ManageCasetModal';
    $('#ManageCasetModal').modal({
      show: 'true'
    });
  }

  public saveComment(model: NotesCriticalCases) {
    // console.log(model);
    this.loading = true;
    this._notesService.postNotesCriticalCases(model).pipe(
      finalize(() => {
        this.getGeneralCases();
        this.loading = false;
      })
    ).subscribe((resp : NotesCriticalCases) => {
      // console.log(resp);
    });
  }

  public updateComment(model: NotesCriticalCases) {
    this.loading = true;
    this._notesService.putNotesCriticalCases(model).pipe(
      finalize(() => {
        this.getGeneralCases();
        this.loading = false;
      }) 
    ).subscribe((resp : boolean) => {
      
    });
  }

  public nextPage(event) {
    this.actualPage = event.page + 1;
    this.getGeneralCases();
  }

  public deleteNote(idNote: number) {
    this.loading = true;
    this._notesService.deleteNotesCriticalCases(idNote).pipe(
      finalize(() => {
        this.getGeneralCases();
        this.loading = false;
      }) 
    ).subscribe((resp : boolean) => {
    });
  }
}
