import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Headers, RequestOptions, Http } from '@angular/http';
import { GlobalConstants } from '../../../shared/constants';
import { ServiceService } from '../service.service';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable()
export class LoginService<T> extends ServiceService<T> {

  constructor(http: HttpClient) {
    super();
    this.apiUrl = environment.apiLoginUrl;
    this.httpClient = http;
    this.resource = 'oauth2/token';
  }

  private grantType = 'password';

  public authenticate(usuario: string, clave: string): Observable<any> {
    const headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded'});
    const options = new RequestOptions({ headers });
    const body = `username=${usuario}&password=${clave}&grant_type=${this.grantType}&client_id=${GlobalConstants.CLIENT_ID}`;
    return this.executePost('', body, options);
  }

}
