  constructor(private _router : Router,
    private _reportService: RealStateReportsService,
    private _notesService: NotesCriticalCasesService,
    private _generalService: GeneralDataReportsService,
    private _userService: UserService<any>)
    { 
      this.commentForm = new FormGroup({
        description: new FormControl (''),
        // <sendTo>: new FormControl (''),
        causal : new FormControl(''),
        idCase : new FormControl(''),
        idNote : new FormControl(''),
      });
      this.sellerCases = new Paginator();
      this.getCodSeller();
      this.getCauses();
    }

  ngOnInit() {
  }

  public getCauses() {
    this._generalService.getCauses()
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: Causes[]) => {
      this.causes = resp;
    },
    error => {
      this.error = error.error;
    });
  }

  private getCodSeller() {
    this.loading = true;
    this._generalService.GetCodSellerAndRegion(GlobalConstants.obtenerIdUsuario())
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: any) => {
      this.codSeller = resp['codSeller'];
      this.getSellerCases();
      this.getSellerProducts();
      this.getSellerRegions();
      this.getSellerSegments();
      this.getSellerPortfolioPurchase();
    },
    error => {
      this.error = error.error;
    });
  }

  private getSellerSegments() {
    this.loading = true;
    this._reportService.getSellerSegments(null, null, parseInt(this.codSeller))
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: SellerSegments[]) => {
      this.sellerSegments = resp;
      let segments = [];
      let segmentsDisbursements = [];
      let cases = [];
      let segmentsL = [];
      let segmentsDisbursementsL = [];
      let casesL = [];
      this.sellerSegments.filter(element => element.Tipo == "HIPOTECARIO").forEach(element => {
        segments.push(element.Segmento);
        segmentsDisbursements.push(element.ValorFinanciar);
        cases.push(element.Casos);
      });
      this.sellerSegments.filter(element => element.Tipo == "LEASING").forEach(element => {
        segmentsL.push(element.Segmento);
        segmentsDisbursementsL.push(element.ValorFinanciar);
        casesL.push(element.Casos);
      });
      // this.sellerSegments.forEach(element => {
      //   segments.push(element.Segmento);
      //   segmentsDisbursements.push(element.ValorFinanciar);
      //   cases.push(element.Casos);
      // });

      //HIPOTECARIO
      this.barChartDataSegments = [
        { data: cases, label: 'Casos', backgroundColor: 'rgba(255, 194, 14, 0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(255, 194, 14, 1)' }
      ];
      this.barChartDataSegmentsDisbursements = [
        { data: segmentsDisbursements, label: 'Casos', backgroundColor: 'rgba(255, 194, 14, 0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(255, 194, 14, 1)' }
      ];
      this.barChartLabelsSegments = segments;

      //LEASING
      this.barChartDataSegmentsL = [
        { data: casesL, label: 'Casos', backgroundColor: 'rgba(167, 169, 172 ,0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(167, 169, 172 , 1)' }
      ];
      this.barChartDataSegmentsDisbursementsL = [
        { data: segmentsDisbursementsL, label: 'Casos', backgroundColor: 'rgba(167, 169, 172 ,0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(167, 169, 172 1)' }
      ];
      this.barChartLabelsSegmentsL = segmentsL;
    },
    error => {
      this.error = error.error;
    });
  }

  private getSellerRegions() {
    this.loading = true;
    this._reportService.getSellerRegions(null, null, parseInt(this.codSeller))
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: SellerRegions[]) => {
      this.sellerRegions = resp;
      this.sellerRegions.forEach(element => {
        this.pieCharRegionsLabels.push(element.Region_Ventas);
        this.pieChartRegionsData.push(element.Casos);
      });
    },
    error => {
      this.error = error.error;
    });
  }

  private getSellerProducts() {
    this.loading = true;
    this._reportService.getSellerProducts(null, null, parseInt(this.codSeller))
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: SellerProducts[]) => {
      this.sellerProducts = resp;
      let products = [];
      let cases = [];
      let productsDisbursements = [];
      let productsL = [];
      let casesL = [];
      let productsDisbursementsL = [];
      this.sellerProducts.filter(element => element.Tipo == 'HIPOTECARIO').forEach(element => {
        products.push(element.Producto);
        cases.push(element.Casos);
        productsDisbursements.push(element.ValorFinanciar);
      });
      this.sellerProducts.filter(element => element.Tipo == 'LEASING').forEach(element => {
        productsL.push(element.Producto);
        casesL.push(element.Casos);
        productsDisbursementsL.push(element.ValorFinanciar);
      });

      //HIPOTECARIO
      this.barChartDataProducts = [
        { data: cases, label: 'Casos', backgroundColor: 'rgba(255, 194, 14, 0.7)', yAxisID: 'A', hoverBackgroundColor: 'rgba(255, 194, 14, 1)' }
      ];
      this.barChartLabelsProducts = products;
      this.barChartDataProductsDisbursements = [
        { data: productsDisbursements, label: 'Casos', backgroundColor: 'rgba(255, 194, 14, 0.7)', yAxisID: 'A', hoverBackgroundColor: 'rgba(255, 194, 14, 1)' }
      ];

      //LEASING
      this.barChartDataProductsL = [
        { data: casesL, label: 'Casos', backgroundColor: 'rgba(167, 169, 172 ,0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(167, 169, 172 , 1)' }
      ];
      this.barChartLabelsProductsL = productsL;
      this.barChartDataProductsDisbursementsL = [
        { data: productsDisbursementsL, label: 'Casos', backgroundColor: 'rgba(167, 169, 172 ,0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(167, 169, 172 , 1)' }
      ];
    },
    error => {
      this.error = error.error;
    });
  }

  public sellerName : string;
  public cancelCases: number = 0;
  public causes: Causes[] = [];

  private getSellerCases() {
    this.loading = true;
    this._reportService.getSellerCases(this.actualPage, 10, null, null, parseInt(this.codSeller))
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: Paginator<SellerCases>) => {
      this.sellerCases = resp;
      this.amountCases = 0;
      this.sellerCases.Result.forEach(element => {
        this.amountCases += element.tracking_valor_financiar;
      });
      this.cancelCases = this.sellerCases.Result.filter(element => element.Estado_Solicitud == 'CANCELADO').length;
      this._userService.getUserInfo(GlobalConstants.obtenerIdUsuario()).subscribe((user: any) => {
        this.sellerName = String(user.UserWeb).split(' ')[0];
      });
      
      this.totalCases = this.sellerCases.TotalRecords;
    },
    error => {
      this.error = error.error;
    });
  }

  private getSellerPortfolioPurchase() {
    this.loading = true;
    this._generalService.getPortfolioPurchase(null, parseInt(this.codSeller))
    .pipe(
      finalize(() => {
        this.loading = false;
      })
    ).subscribe((resp: PortfolioPurchase[]) => {
      this.sellerPortfolioPurchse = resp;
      let portfolioPurchaseCases = 0;
      let portfolioPurchaseDesem = 0;
      this.barChartLabelsPortfolioPurchase = [];
      this.barChartDataPortfolioPurchase = [];
      this.sellerPortfolioPurchse.forEach(element => {
        portfolioPurchaseCases += element.casos;
        portfolioPurchaseDesem += element.vlr_desembolso;
      });
      this.barChartLabelsPortfolioPurchase.push('Casos','Desembolsos');
      let portfolioPurchases: number[] = [];
      portfolioPurchases.push(portfolioPurchaseCases, portfolioPurchaseDesem);
      this.barChartDataPortfolioPurchase = [
        { data: portfolioPurchases, label: 'Casos', backgroundColor: 'rgba(51, 80, 106, 0.8)', yAxisID: 'A', hoverBackgroundColor: 'rgba(51, 80, 106, 1)'}
        // { data: portfolioPurchaseDesem, label: 'Desembolsos', backgroundColor: 'rgba(38, 34, 97, 0.8)', yAxisID: 'B', hoverBackgroundColor: 'rgba(38, 34, 97, 1)', }
      ];
    },
    error => {
      this.error = error.error;
    });
  }

