import { Component, OnInit, Input } from '@angular/core';
import { PaginationInstance } from 'ngx-pagination';

@Component({
  selector: 'app-data-table-pagination',
  templateUrl: './data-table-pagination.component.html',
  styleUrls: ['./data-table-pagination.component.css']
})
export class DataTablePaginationComponent implements OnInit {

  constructor() { }

  @Input()
  public pageLink: string;
  @Input()
  public data: Array<any>;
  @Input()
  public parameterName: string;
  @Input()
  public columnNames: Array<any>;
  @Input()
  public columnProperties: Array<any>;
  public maxSizePagination = '6';

  // tslint:disable-next-line: member-ordering
  private paginationConfig: PaginationInstance = {
    id: 'advanced',
    itemsPerPage: 10,
    currentPage: 1
  };

  private labels: object = {
    previousLabel: 'Atrs',
    nextLabel: 'Siguiente',
    screenReaderPaginationLabel: 'Pagination',
    screenReaderPageLabel: 'page',
    screenReaderCurrentLabel: `You're on page`
  };

  ngOnInit() {}

  // tslint:disable-next-line: variable-name
  private onPageChange(number: number) {
    this.paginationConfig.currentPage = number;
  }

}
