import { Component, OnInit, Console } from '@angular/core';
import { finalize } from 'rxjs/operators';
import 'rxjs/add/operator/finally';

import { CreditOrderService } from 'src/app/core/services/workFlowModule/credit-order.service';
import { CreditOrderList } from 'src/app/core/models/credit-order-list.model';
import { GlobalConstants } from 'src/app/shared/constants';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.css']
})
export class ScheduleComponent implements OnInit {

  public creditOrders: Array<CreditOrderList> = [];
  public columnNames: Array<any> = [];
  public columnProperties: Array<any> = [];
  public pageLink: string;
  public parameterName: string;
  public totalCredits = 0;
  public loading: boolean;
  public message: string;

  constructor(private creditOrderService: CreditOrderService<CreditOrderList>) {
    this.loading = false;
  }

  ngOnInit() {

    this.columnNames = ['Nmero Radicado', 'Nombre Cliente', 'Documento Identificacin', 'Hora Contacto',
    'Fecha Creacin', 'Estado Solicitud', 'Fecha Modificacin', 'Accin'];
    this.columnProperties = ['FiledNumber', 'ClientName', 'ClientDocumentId', 'ContactHour', 'CreationDate', 'Proccess', 'UpdateDate'];
    this.pageLink = './';
    this.parameterName = 'IdOrder';
    this.getSchedule();
  }

  private getSchedule() {
    this.loading = true;
    const userId = GlobalConstants.obtenerIdUsuario();
    this.creditOrderService.executeGet('getSchedule?userId=' + userId).pipe(
      finalize (
        () => { this.loading = false;
      })
    ).subscribe(
      (credits: Array<CreditOrderList>) => {
        this.creditOrders = credits;
      },
      error => {
        console.log(error.type);
      }
    );
  }

  private search(data: any) {
    this.loading = true;
    if (data !== '') {
      this.creditOrderService.getCreditOrderByFilter(data).pipe (
        finalize (
          () => this.loading = false
        )
      ).subscribe(
        (credits: Array<CreditOrderList>) => {
          this.message = '';
          this.creditOrders = credits;
        },
        error => {
          this.message = 'No se han encontrado resultados por: ' + data;
          this.getSchedule();
        }
      );
    } else {
      this.message = '';
      this.getSchedule();
    }
  }

}
