import { NgModule,Component, OnInit } from '@angular/core';
import { GlobalConstants } from 'src/app/shared/constants';
import { ActivatedRoute } from '@angular/router';
import { ClientOrderService } from 'src/app/core/services/workFlowModule/client-order.service';
import { ClientOrder } from 'src/app/core/models/client-order.model';
import { finalize } from 'rxjs/operators';
import { CreditOrder } from 'src/app/core/models/credit-order.model';
import { CreditOrderService } from 'src/app/core/services/workFlowModule/credit-order.service';
import { PropertyService } from 'src/app/core/services/workFlowModule/property.service';
import { Property } from 'src/app/core/models/property.model';

import { FormsModule ,FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import {formatCurrency, getCurrencySymbol} from '@angular/common';

import { AppraisalService } from 'src/app/core/services/workFlowModule/appraisal.service';
import { Appraisal } from 'src/app/core/models/appraisal.model';

import { TypeNoveltyService } from 'src/app/core/services/workFlowModule/typenovelty.services';
import { TypeNovelty } from 'src/app/core/models/typenovelty.models';

import { NoveltyCreditAppraisal } from 'src/app/core/models/novelty-credit-appraisal.models';
import { NoveltyCreditAppraisalService } from 'src/app/core/services/workFlowModule/novelty-credit-appraisal.service';
import { UserService } from 'src/app/core/services/userModule/user.service';

@NgModule({
  imports: [
    FormGroup
  ]
})

@Component({
  selector: 'app-credit-information',
  templateUrl: './credit-information.component.html',
  styleUrls: ['./credit-information.component.css']
})

export class CreditInformationComponent implements OnInit {

  public idOrder: string;
  public filedNumber: string;
  public clientOrder: ClientOrder;
  public clientsOrder: Array<ClientOrder> = [];
  public creditOrder: CreditOrder;
  public property: Property;
  public loading: boolean;
  public CalculateForm: FormGroup;
  public ReconsiderationForm: FormGroup;
  public appraisal: Appraisal[] =[];
  public typesNovelties: TypeNovelty[] =[];
  public vfinanciado = 0;
  public vcatastro = 0;
  public vcomercial = 0;
  public resultado = 0;
  public vtexto = '';
  public ShowAvaluos:boolean=false;
  currTypeNovelty: number=0;
  currNovelty: number=0;
  public noveltyCredit: NoveltyCreditAppraisal;
  public userName: string;
  public idUser: string;
 
  constructor(private route: ActivatedRoute,
              private userService: UserService<any>,
              private clientOrderService: ClientOrderService<ClientOrder>,
              private AppraisalService: AppraisalService<Appraisal>,
              private TypeNoveltyService: TypeNoveltyService<TypeNovelty>,
              private noveltyCreditAppraisalservice: NoveltyCreditAppraisalService<NoveltyCreditAppraisal>,
              private propertyService: PropertyService<Property>,
              private creditOrderService: CreditOrderService<CreditOrder>,
              private formBuilder: FormBuilder) {
             // Inicializacin de variables de pgina
              this.CalculateForm =  new FormGroup({
               
                vfinanciado: new FormControl('', Validators.required),
                vcatastro: new FormControl('', Validators.required),
                vresultado: new FormControl(''),
                vtexto: new FormControl(''),
                vcomercial: new FormControl('', Validators.required)});
                this.ReconsiderationForm= new FormGroup({
                  TiposNovedades: new FormControl (''),
                  AvaluosNovedades: new FormControl (''),
                  NroPrg: new FormControl (''),
                });
              this.loading = false;
              this.idUser='';
  }
  
  ngOnInit() {
    this.route.params.subscribe( parameters => {
      this.idOrder = parameters['idOrder'];
      this.getClientOrder();
      this.getCreditOrder();
      this.getProperty();
    });
    this.getTypesNovelties();     
    this.getAppraisals();     
   
  }
  
 updateValue(event,nombrec: string) {
  let val = parseInt(event.target.value);
  if (Number.isNaN(val)) {
    val = 0;
  }
  this.CalculateForm.get(nombrec).setValue(formatCurrency(val, 'en-US', getCurrencySymbol('USD', 'wide')));
}

  private calcular() {
    this.vfinanciado =  parseInt(this.CalculateForm.get('vfinanciado').value.replace(/[$,]/g, "").split(".")[0]);
     this.vcomercial = ( this.CalculateForm.get('vcomercial').value.replace(/[$,]/g, "").split(".")[0]) * 0.51;
    
    this.vcatastro = parseInt(this.CalculateForm.get('vcatastro').value.replace(/[$,]/g, "").split(".")[0]);
    const valores = [this.vcatastro, this.vfinanciado , this.vcomercial];
    const textos = ['Valor Catastral', 'Valor financiado' , 'Avalo comercial'];
    this.resultado = Math.max.apply(null, valores);
    this.CalculateForm.get('vresultado').setValue(formatCurrency(this.resultado, 'en-US', getCurrencySymbol('USD', 'wide')));
    
    this.CalculateForm.get('vtexto').setValue(textos[valores.indexOf(this.resultado)]);
  }

  validateForm(){
    return true;
  }
  onSelectState(valor){
    this.currTypeNovelty=parseInt(valor);
    this.ShowAvaluos= true;
 }
 onSetValue(valor){
  this.currNovelty=parseInt(valor);
 }
 refresh(): void {
  window.location.reload();
}
  onValidate(){
    if (this.validateForm()) {
      this.mapNewModel();
      this.saveNoveltyAppraisal();
    }
  }
  private mapNewModel() {
    if (this.noveltyCredit == null) {
      this.noveltyCredit = new NoveltyCreditAppraisal();
    }
    this.userName = GlobalConstants.obtenerNombreUsuario();
    this.noveltyCredit.NoveltyId=this.currNovelty;
    this.noveltyCredit.NroPRG = this.ReconsiderationForm.get('NroPrg').value;
    this.noveltyCredit.Usuario = this.userName;
    
   }
  private saveNoveltyAppraisal() {
    this.loading = false;
    this.noveltyCreditAppraisalservice.post(this.noveltyCredit, 'save').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response !== null) {
        this.refresh();
        } else {
          console.log("error");
        }
      },
      error => {
        console.log(error.error);      }
    );
    this.refresh();
  }

  private getTypesNovelties() {
    this.loading = true;
    this.TypeNoveltyService.getAllTypeNovelties().pipe(
      finalize(() => {
        this.loading = false;
      }) 
    ).subscribe(
      (data: TypeNovelty[]) => {
        this.typesNovelties = data;
        //console.log(this.appraisal);
        //this.fillClientForm();
        //this.getProperty();
      },
      error => {
        console.log(error.error);
      }
    );
  }
  private getAppraisals() {
    this.loading = true;
    this.AppraisalService.getAllAppraisals().pipe(
      finalize(() => {
        this.loading = false;
      }) 
    ).subscribe(
      (data: Appraisal[]) => {
        this.appraisal = data;
        //console.log(this.appraisal);
        //this.fillClientForm();
        //this.getProperty();
      },
      error => {
        console.log(error.error);
      }
    );
  }

  private getProperty() {
    this.loading = true;
    this.propertyService.getPropertyByOrderId(this.idOrder + '').pipe (
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (property: Property) => {
        this.property = property;
      },
      error => {
        console.log(error.error);
      }
    );
  }

  private getClientOrder() {
    this.loading = true;
    this.clientOrderService.getClientByIdOrder(this.idOrder).pipe (
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (client: ClientOrder) => {
        this.clientOrder = client;
      },
      error => {
        console.log(error);
      }
    );
  }

  private getClientsOrder() {
    this.loading = true;
    this.clientOrderService.getClientsByFiledNumber(this.filedNumber).pipe (
      finalize(
        () => this.loading = false
      )
    ).subscribe(
      (clients: Array<ClientOrder>) => {
        this.clientsOrder = clients;
      },
      error => {
        console.log(error);
      }
    );
  }

  private getCreditOrder() {
    this.loading = true;
    this.creditOrderService.get(this.idOrder, 'getById?id=').pipe (
      finalize(
        () => {
            this.loading = false;
            this.filedNumber = this.creditOrder.FiledNumber;
            this.getClientsOrder();
        }
      )
    ).subscribe((credit: CreditOrder) => {
        this.creditOrder = credit;
      },
      error => {
        console.log(error);
      }
    );
  }
}
