import { Component, OnInit } from '@angular/core';
import { GlobalConstants } from '../../constants';
import { UserService } from 'src/app/core/services/userModule/user.service';
import { Router } from '@angular/router';
import { OptionRole } from 'src/app/core/models/optionRole';
import { MenuItem } from 'primeng/api';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {
  //#region Declaracin de variables globales
  public idUser: string;
  public name: string;
  public userName: string;
  public loading: boolean;
  public listOptions: OptionRole[];
  //#endregion

  constructor(private userService: UserService<any>,
              private router: Router) {
    this.idUser = '';
    this.name = '';
    this.loading = false;
    this.listOptions = [];
  }

  //#region mtodos pblicos
  public ngOnInit() {
    this.loadInformation();
  }
  //#endregion

  //#region Mtodos privados
  private loadInformation(): void {
    this.loading = true;

    this.idUser = GlobalConstants.obtenerIdUsuario();
    this.userService.getUserInfo(this.idUser).subscribe(
      (user: any) => {
        this.name = user.UserWeb;
        this.userName = user.UserName;

        if (user.FirstEntry === true ) {
          this.router.navigateByUrl('../../../security/ChangePassword');
        } else {

          this.userService.getModulesUser(this.idUser).subscribe(
            (options: OptionRole[]) => {
              if (options.length > 0) {
                options.forEach((module: OptionRole) => {
                  this.listOptions = [...this.listOptions, new OptionRole(module)];
                });
              }
          });

        }
    });

  }
  //#endregion

}
