import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { ProvidersService } from 'src/app/core/services/workFlowModule/providers.service';
import { ProviderTypeService } from 'src/app/core/services/workFlowModule/provider-type.service';
import { ProviderType } from 'src/app/core/models/provider-type.model';
import { FormBuilder, FormGroup } from '@angular/forms';
import { finalize } from 'rxjs/operators';
import { Provider } from 'src/app/core/models/provider.model';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-provider-form',
  templateUrl: './provider-form.component.html',
  styleUrls: ['./provider-form.component.css']
})
export class ProviderFormComponent implements OnInit {

  @Input()
  public provider: Provider;
  @Input()
  public idOrder: number;
  @Output()
  public reload = new EventEmitter<boolean>();
  public providerForm: FormGroup;
  public loading: boolean;
  public providerTypes: Array<ProviderType> = [];
  public error: string;

  constructor(private providerService: ProvidersService<Provider>,
              private providerTypeService: ProviderTypeService<ProviderType>,
              private toast: MessageService,
              private formBuilder: FormBuilder) {
    this.providerForm = this.formBuilder.group({
      providerName :  '',
      idOrder:  '',
      providerId:  '',
      providerDocument:  '',
      providerTypeDocument:  '',
      providerEmail:  '',
      providerPhone:  '',
      providerMovil:  '',
      providerGender:  '',
      providerTypeId:  '',
      providersQuantity:  '',
      inRepresentation:  '',
      alternativeContactName:  '',
      alternativeContactMovil:  '',
      alternativeContactPhone:  '',
      alternativeContactEmail:  ''
    });
  }

  ngOnInit() {
    this.getProviderTypes();
  }

  private getProviderTypes() {
    this.loading = true;
    this.providerTypeService.executeGet('getAll').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
    (providerTypes: Array<ProviderType>) => {
        if (providerTypes != null) {
          this.providerTypes = providerTypes;
        } else {
          this.error = 'No se han encontrado tipos de proveedor';
        }
        this.fillForm();
      },
      error => {
      }
    );
  }

  private updateProvider() {
    this.providerService.update(this.provider, 'update').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response != null) {
          this.provider = response;
          this.cleanForm();
          this.reload.next(true);
        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }

  private saveProvider() {
    this.providerService.post(this.provider, 'save').pipe(
      finalize (
        () => this.loading = false
      )
    ).subscribe(
      (response: any) => {
        if (response !== null) {
          this.provider = response;
          this.cleanForm();
          this.reload.next(true);
        } else {
          this.error = 'Ha ocurrido un error';
        }
      },
      error => {
        this.error = error.error;
      }
    );
  }
  refresh(): void {
    window.location.reload();
}
  private onSubmit(data: any) {
    if (this.validateForm(data)) {
      this.mapNewModel(data);
      if (this.provider.ProviderId > 0) {
        this.updateProvider();
      } else {
        this.saveProvider();
      }
      this.refresh();
    }
  }

  private validateForm(data: any) {
    if (data.providerName !== '' && data.providerDocument !== '' && data.providerTypeDocument !== '' &&
        data.providerEmail !== '' && data.providerPhone !== '' && data.providerMovil !== '' && data.providerTypeId !== '' &&
        data.providerGender !== '') {
          return true;
    }
  }

  private mapNewModel(data: any) {
    if (this.provider == null) {
      this.provider = new Provider();
    }
    this.provider.ProviderName = data.providerName;
    this.provider.CreditOrderId = this.idOrder;
    this.provider.DocumentNumber = data.providerDocument;
    this.provider.DocumentType = data.providerTypeDocument;
    this.provider.Email = data.providerEmail;
    this.provider.Phone = data.providerPhone;
    this.provider.Movil = data.providerMovil;
    this.provider.Gender = data.providerGender;
    this.provider.ProviderTypeId = data.providerTypeId;
    this.provider.PropertyProviderQuantity = data.providersQuantity;
    this.provider.InRepresentation = data.inRepresentation;
    this.provider.AlternativeContactName = data.alternativeContactName;
    this.provider.AlternativeContactMovil = data.alternativeContactMovil;
    this.provider.AlternativeContactPhone = data.alternativeContactPhone;
    this.provider.AlternativeContactEmail = data.alternativeContactEmail;
  }

  private fillForm() {
    if (this.provider != null) {
      this.providerForm = this.formBuilder.group({
        providerName: this.provider.ProviderName,
        providerDocument: this.provider.DocumentNumber,
        providerTypeDocument: this.provider.DocumentType,
        providerEmail: this.provider.Email,
        providerPhone: this.provider.Phone,
        providerMovil: this.provider.Movil,
        providerGender: this.provider.Gender,
        providerTypeId: this.provider.ProviderTypeId,
        providersQuantity: this.provider.PropertyProviderQuantity,
        inRepresentation: this.provider.InRepresentation,
        alternativeContactName: this.provider.AlternativeContactName,
        alternativeContactMovil: this.provider.AlternativeContactMovil,
        alternativeContactPhone: this.provider.AlternativeContactPhone,
        alternativeContactEmail: this.provider.AlternativeContactEmail
      });
    }
  }

  private cleanForm() {
    this.providerForm = this.formBuilder.group({
      providerName :  '',
      idOrder:  '',
      providerId:  '',
      providerDocument:  '',
      providerTypeDocument:  '',
      providerEmail:  '',
      providerPhone:  '',
      providerMovil:  '',
      providerGender:  '',
      providerTypeId:  '',
      providersQuantity:  '',
      inRepresentation:  '',
      alternativeContactName:  '',
      alternativeContactMovil:  '',
      alternativeContactPhone:  '',
      alternativeContactEmail:  ''
    });
  }

}
